import * as auth from './user-services';

export async function createItem(item) {
    const result = await fetch(`/api/item`, {
        method: 'post',
        body: JSON.stringify(item),
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function getDefaultItems() {
    const result = await fetch(`/api/item`, {
        method: 'get',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function getCustomItems(userId) {
    const result = await fetch(`/api/item/custom/${userId}`, {
        method: 'get',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function getCharacterItems(characterId) {
    const result = await fetch(`/api/item/${characterId}`, {
        method: 'get',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function updateItem(item) {
    const result = await fetch(`/api/item/${item._id}`, {
        method: 'put',
        body: JSON.stringify(item),
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function addItem(characterId, item) {
    const result = await fetch(`/api/item/add/${characterId}`, {
        method: 'put',
        body: JSON.stringify(item),
        header: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function deleteCustomItem(itemId) {
    const result = await fetch(`/api/item/${itemId}`, {
        method: 'delete',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function deleteAllItems(userId) {
    const result = await fetch(`/api/item/all/${userId}`, {
        method: 'delete',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}