import React, {useState, useContext} from 'react';
import { FaTrashAlt, FaPencilAlt, FaTag } from 'react-icons/fa';
import * as characterServices from './../../../Services/character-services';
import * as gameServices from './../../../Services/game-services';
import Actor from './Actor';
 
const ActorList = ({actors, selectActor, deleteActor, editActor}) => 
{
    return (
        <>
        <div className="action-list item-list mb-3">
        {actors.map((actor, index) => (
                <div className="card-item col media py-3" key={index}>
                <div className="card-info media-body">
                    <Actor actor={actor}/>
                    
                    <div className="p-2">
                        <button onClick={() => deleteActor(actor)} className={"col-sm-2 mx-3 card-delete btn btn-sm btn-danger "+
                            ((actor.isDefault || gameServices.isInGame())?"hidden-button":"")} >
                            Delete <FaTrashAlt />
                        </button>
                        <button onClick={() => editActor(actor)} className={"col-sm-2 mx-3 card-delete btn btn-sm btn-warning " + 
                            (gameServices.isInGame()?"hidden-button":"")}>
                            Edit <FaPencilAlt />
                        </button>
                        <button onClick={() => selectActor(actor)} className={"col-sm-2 mx-3 card-delete btn btn-sm btn-primary "+
                            ((characterServices.hasCurrCharacter() && !gameServices.isInGame())?"":"hidden-button")} >
                            Select <FaTag />
                        </button>
                    </div>
                </div>
            </div>
        ))}
        </div>
        </>
    );
};

export default ActorList;