import React, {useState, useContext} from 'react';
import { FaTrashAlt, FaPencilAlt, FaTimesCircle } from 'react-icons/fa';
import * as gameServices from './../../../Services/game-services';
import Actor from './Actor';
 
const ActorWrapperList = ({actors, dropActor, deleteActor, editActor}) => 
{
    return (
        <>
        <div className="card-list item-list mb-3">
        {actors.map((actor, index) => (
            <div className="card-item col media py-3" key={index}>
                <div className="card-info media-body">
                    <Actor actor={actor.actor} name={actor.wrapper.name}/>

                    <div className="row py-1">
                        <div className="card-details col-sm-4">
                            <span className="label-item">Initiative Score: </span>
                        {actor.wrapper.initActual}
                        </div>

                        <div className="card-details col-sm-5">
                            <span className="label-item">Is Hidden To Others: </span>
                            {actor.wrapper.isHidden ? "Yes" : "No"}
                        </div>
                    </div>
                        
                    <div className="p-2">
                        <button onClick={() => deleteActor(actor.actor)} className={"col-sm-2 mx-3 card-delete btn btn-sm btn-danger " +
                            ((!actor.actor.isDefault && deleteActor)? "":"hidden-button")}  >
                            Delete <FaTrashAlt />
                        </button>
                        <button className={"col-sm-2 mx-3 card-delete btn btn-sm btn-warning "+(editActor? "":"hidden-button")}
                            onClick={() => editActor(actor.actor, actor.wrapper)} >
                            Edit <FaPencilAlt />
                        </button>
                        <button className={"col-sm-2 mx-3 card-delete btn btn-sm btn-secondary "+(dropActor? "":"hidden-button")}
                            onClick={() => dropActor(actor.actor, actor.wrapper)} >
                            Deselect <FaTimesCircle />
                        </button>
                    </div>         
                </div>
            </div>
        ))}
        </div>
        </>
    );
};

export default ActorWrapperList;





