import React, {useState, useContext} from 'react';
import { FaTrashAlt, FaPencilAlt, FaTimesCircle } from 'react-icons/fa';
import * as gameServices from './../../../Services/game-services';
import Status from './Status'; 
const StatusWrapperList = ({status, dropStatus, deleteStatus, editStatus}) => 
{
    return (
        <>
        <div className="card-list item-list mb-3">
        {status.map((status, index) => (
            <div className="card-item col media py-3" key={index}>
                <div className="card-info media-body">
                    <Status status={status.status} />

                    <div>
                        <div className="card-details">
                            <span className="label-item">End Date: </span>
                            {status.wrapper.endDate}
                        </div>
                    </div>

                    <div className="p-2">
                        <button onClick={() => deleteStatus(status.status)} className={"col-sm-2 mx-3 card-delete btn btn-sm btn-danger " +
                            ((!status.status.isDefault && deleteStatus)? "":"hidden-button")}  >
                            Delete <FaTrashAlt />
                        </button>
                        <button className={"col-sm-2 mx-3 card-delete btn btn-sm btn-warning "+(editStatus? "":"hidden-button")}
                            onClick={() => editStatus(status.status, status.wrapper)} >
                            Edit <FaPencilAlt />
                        </button>
                        <button className={"col-sm-2 mx-3 card-delete btn btn-sm btn-secondary "+(dropStatus? "":"hidden-button")}
                            onClick={() => dropStatus(status.status, status.wrapper)} >
                            Deselect <FaTimesCircle />
                        </button>
                    </div>
                </div>
            </div>
        ))}
        </div>
        </>
    );
};

export default StatusWrapperList;