import React, {useState, useContext} from 'react';
import { FaTrashAlt, FaPencilAlt, FaTag } from 'react-icons/fa';
import * as characterServices from './../../../Services/character-services';
import * as gameServices from './../../../Services/game-services';
import Status from './Status';
 
const StatusList = ({status, selectStatus, deleteStatus, editStatus}) => 
{
    return (
        <>
        <div className="card-list item-list mb-3">
        {status.map((status, index) => (
            <div className="card-item col media py-3" key={index}>
                <div className="card-info media-body">
                    <Status status={status} />
                        
                    <div className="p-2">
                        <button onClick={() => deleteStatus(status)} className={"col-sm-2 mx-3 card-delete btn btn-sm btn-danger "+
                            ((status.isDefault || gameServices.isInGame())?"hidden-button":"")} >
                            Delete <FaTrashAlt />
                        </button>
                        <button onClick={() => editStatus(status)} className={"col-sm-2 mx-3 card-delete btn btn-sm btn-warning " + 
                            (gameServices.isInGame()?"hidden-button":"")}>
                            Edit <FaPencilAlt />
                        </button>
                        <button onClick={() => selectStatus(status)} className={"col-sm-2 mx-3 card-delete btn btn-sm btn-primary "+
                            ((characterServices.hasCurrCharacter() && !gameServices.isInGame())?"":"hidden-button")} >
                            Select <FaTag />
                        </button>
                    </div>
                </div>
            </div>
        ))}
        </div>
        </>
    );
};

export default StatusList;