import React, {useState, useContext} from 'react';

const ItemSearch = ({orderBy, orderDir, changeOrder, searchItems, 
    showCustom, showDefault, showConsume, showNonConsume, changeShow}) => {

    return (
        <>
        <div className="search-actions row justify-content-center my-4">
        <div className="col-md-12">
        <div className="input-group">
            <input type="text" className="SearchAction form-control col-md-6 text-md-right" aria-label="Search Actions"
                onChange={e => searchItems(e.target.value)}/>
            <div className="btn-group input-group-append col-md-4">
                <button type="button" className="btn btn-primary dropdown-toggle" data-toggle="dropdown"
                    aria-haspopup="true"aria-expanded="false"> 
                    Sort by: <span className="caret" />
                </button>

                <div className="sort-menu dropdown-menu dropdown-menu-right">
                    <button className={'sort-by dropdown-item ' + (orderBy === 'name' ? 'active' : '')}
                        onClick={e =>changeOrder('name', orderDir)} href="#">
                        Name
                    </button>
                    <button className={'sort-by dropdown-item ' + (orderBy === 'actionType' ? 'active' : '')}
                        onClick={e => changeOrder('actionType', orderDir)} href="#">
                        Action Type
                    </button>
                    <button className={'sort-by dropdown-item ' + (orderBy === 'goldValue' ? 'active' : '')}
                        onClick={e => changeOrder('goldValue', orderDir)} href="#">
                        Gold Value
                    </button>
                    <button className={'sort-by dropdown-item ' + (orderBy === 'weight' ? 'active' : '')}
                        onClick={e => changeOrder('weight', orderDir)} href="#">
                        Weight
                    </button>
                    <div role="separator" className="dropdown-divider" />
                    <button className={'sort-by dropdown-item ' + (orderDir === 'asc' ? 'active' : '')}
                        onClick={e => changeOrder(orderBy, 'asc')} href="#">
                        Asc
                    </button>
                    <button className={'sort-by dropdown-item ' + (orderDir === 'desc' ? 'active' : '')}
                        onClick={e =>changeOrder(orderBy, 'desc')} href="#" >
                        Desc
                    </button>
                </div>
            </div>

            <div className="btn-group input-group-append col-md-4">
                <button type="button" className="btn btn-primary dropdown-toggle" data-toggle="dropdown"
                    aria-haspopup="true"aria-expanded="false"> 
                    Filter Out: <span className="caret" />
                </button>

                <div className="sort-menu dropdown-menu dropdown-menu-right">
                    <button className={'sort-by dropdown-item ' + ( showDefault ? 'active' : '')}
                        onClick={e => changeShow(showCustom, !showDefault, showConsume, showNonConsume)} href="#">
                        Default Items
                    </button>
                    <button className={'sort-by dropdown-item ' + ( showCustom ? 'active' : '')}
                        onClick={e => changeShow(!showCustom, showDefault, showConsume, showNonConsume)} href="#">
                        Custom Items
                    </button>
                    <button className={'sort-by dropdown-item ' + ( showConsume ? 'active' : '')}
                        onClick={e => changeShow(showCustom, showDefault, !showConsume, showNonConsume)} href="#">
                        Consumables
                    </button>
                    <button className={'sort-by dropdown-item ' + ( showNonConsume ? 'active' : '')}
                        onClick={e => changeShow(showCustom, showDefault, showConsume, !showNonConsume)} href="#">
                        Non-Consumables
                    </button>
                </div>
            </div>
        </div>
        </div>
        </div>
        </>
    )
}

export default ItemSearch;