import React, {useState, useContext, useEffect} from 'react';
import {AppContext} from '../../../Services/AppContext';
import ActionList from './ActionList';
import ActionForm from './ActionForm';
import ActionSearch from './ActionSearch';
import ActionWrapperForm from './ActionWrapperForm';
import ActionWrapperList from './ActionWrapperList';
import ActionWrapperSearch from './ActionWrapperSearch';
import * as actionServices from './../../../Services/action-services';
import * as characterServices from './../../../Services/character-services';
import * as userServices from './../../../Services/user-services';
import * as gameServices from './../../../Services/game-services';
import { find, findIndex, without } from 'lodash';
import { FaChevronDown, FaChevronUp } from 'react-icons/fa';

const ActionPage = () => {
    const contextData = useContext(AppContext)[0];
    const [actions, setActions] = contextData.user.actions;
    const [characterActions, setCharacterActions] = contextData.character.actions;
    const [defaultActions, setDefaultActions] = contextData.default.actions;

    const [user, setUser] = contextData.user.user;
    const [character, setCharacter] = contextData.character.character;

    const [userFormDisplay, setUserFormDisplay] = useState(false);
    const [characterFormDisplay, setCharacterFormDisplay] = useState(false);
    const [bottomHidden, setBottomHidden] = useState(false);
    const [topHidden, setTopHidden] = useState(false);
    const toggleTopList = () => {
        setTopHidden(!topHidden);
    }
    const toggleBottomList = () => {
        setBottomHidden(!bottomHidden);
    }
    const userToggleForm = () => {
        setUserFormDisplay(!userFormDisplay);
    }
    const characterToggleForm = () => {
        setCharacterFormDisplay(!characterFormDisplay);
    }

    const [userQuery, setUserQuery] = useState('');
    const [characterQuery, setCharacterQuery] = useState('');
    const userSearch = (query) => {
        setUserQuery(query);
    }
    const characterSearch = (query) => {
        setCharacterQuery(query);
    }

    const [userOrderBy, setUserOrderBy] = useState('name');
    const [userOrderDir, setUserOrderDir] = useState('asc');
    const [characterOrderBy, setCharacterOrderBy] = useState('name');
    const [characterOrderDir, setCharacterOrderDir] = useState('asc');
    const userChangeOrder = (order, dir) => {
        setUserOrderBy(order);
        setUserOrderDir(dir);
    }
    const characterChangeOrder = (order, dir) => {
        setCharacterOrderBy(order);
        setCharacterOrderDir(dir);
    }
    
    const [userShowCustom, setUserShowCustom] = useState(true);
    const [userShowDefault, setUserShowDefault] = useState(true);
    const [characterShowCustom, setCharacterShowCustom] = useState(true);
    const [characterShowDefault, setCharacterShowDefault] = useState(true);
    const userChangeShow = (showCustom, showDefault) => {
        setUserShowCustom(showCustom);
        setUserShowDefault(showDefault);
    }
    const characterChangeShow = (showCustom, showDefault) => {
        setCharacterShowCustom(showCustom);
        setCharacterShowDefault(showDefault);
    }

    const [editData, setEditData] = useState();
    const [wrapperData, setWrapperData] = useState();
    const [sendData, setSendData] = useState();
    const updateAction = async (updatedAction) => {
        let i = 0;
        for(const action of characterActions) {
            if (action._id == updatedAction) {
                characterActions[i] = updatedAction;
            }
            i++;
        }

        const userIndex = findIndex(actions, {
            _id: updatedAction._id
        });
        if (userIndex > -1) {
            actions[userIndex] = updatedAction;
            setActions([...actions]);
        }
        setEditData();
        await actionServices.updateAction(updatedAction);
    }
    const updateCharacterAction = async (updatedAction, updatedWrapper) => {
        const userIndex = findIndex(actions, {
            _id: updatedAction._id
        });
        if (userIndex > -1) {
            actions[userIndex] = updatedAction;
            setActions([...actions]);
        }

        let i = 0;
        for(const action of characterActions) {
            if (action._id == updatedAction._id) {
                characterActions[i] = updatedAction;
            }
            i++;
        }
        setCharacterActions([...characterActions]);

        updateWrapper(updatedWrapper);
        await actionServices.updateAction(updatedAction);
    }
    const updateWrapper = async (updatedWrapper) => {
        const actionIndex = findIndex(character.action, {actionId: updatedWrapper.actionId});
        if (actionIndex > -1) {
            const newCharacter = character;
            newCharacter.actions[actionIndex] = updatedWrapper;
            setCharacter(newCharacter);
            await characterServices.updateCharacter(newCharacter);
        }
        setEditData();
        setWrapperData()
    }
    const cancelEdit = () => {
        setEditData();
    }
    const cancelCharacterEdit = () => {
        setEditData();
        setWrapperData()
    }
    const editAction = (action) => {
        setEditData(action);
        setUserFormDisplay(true);
        setCharacterFormDisplay(false);
    }
    const editCharacterAction = (action, wrapper) => {
        setEditData(action);
        setWrapperData(wrapper)
        setUserFormDisplay(false);
        setCharacterFormDisplay(true);
    }
    const createAction = async (createdAction) => {
        const newAction = (await actionServices.createAction(createdAction)).action;
        setActions([...actions, newAction]);
        const newUser = user;
        newUser.actionIds.push(newAction._id);
        setUser(newUser);
        await userServices.updateUser(newUser)
        return newAction._id;
    }
    const createCharacterAction = async (newCharacterAction, newActionWrapper) => {
        const newAction = (await actionServices.createAction(newCharacterAction)).action;
        setActions([...actions, newAction]);
        setCharacterActions([...characterActions, newAction])

        const newUser = user;
        newUser.actionIds.push(newAction._id);
        setUser(newUser);
        await userServices.updateUser(newUser)

        const newCharacter = character;
        newCharacter.actions.push({actionId: newAction._id});
        setCharacter(newCharacter);
        await characterServices.updateCharacter(newCharacter);

        return newAction._id;
    }
    const selectAction = async (newCharacterAction) => {
        setCharacterActions([...characterActions, newCharacterAction]);

        const newCharacter = character;
        newCharacter.actions.push({actionId: newCharacterAction._id});
        setCharacter(newCharacter);
        await characterServices.updateCharacter(newCharacter)
    }
    const deleteAction = async (deletedAction) => {
        if (!deletedAction.isDefault) {
                let actionWrapperIndex = -1;
                do {
                    const actionIdIndex = user.actionIds.indexOf(deletedAction._id);
                    if (actionIdIndex > -1) {
                        const newIdList = without(user.actionIds, user.actionIds[actionIdIndex]);
                        const newUser = user;
                        newUser.actionIds = newIdList;
                        setUser(newUser);
                    }
                    
                    const actionsIndex = findIndex(actions, {
                        _id: deletedAction._id
                    });
                    if (actionsIndex > -1) {
                        const newList = without(actions, actions[actionsIndex]);
                        setActions([...newList]);
                    }

                    const charActionsIndex = findIndex(characterActions, {
                        _id: deletedAction._id
                    });
                    if (charActionsIndex > -1) {
                        const newCharList = without(characterActions, characterActions[charActionsIndex]);
                        setCharacterActions([...newCharList]);
                    }
                    
                    if (character) {
                        actionWrapperIndex = findIndex(character.actions, {
                            actionId: deletedAction._id
                        });
                        if (actionWrapperIndex > -1) {
                            const newCharacter = character;
                            newCharacter.actions = without(character.actions, character.actions[actionWrapperIndex]);;
                            setCharacter(newCharacter);
                        };
                    }
                } while (actionWrapperIndex > -1)
                await userServices.updateUser(user);
                if (character) {
                    await characterServices.updateCharacter(character);
                }
                await actionServices.deleteCustomAction(deletedAction._id);
        } else {
            console.log("can't delete default");
        }
    }
    const dropAction = async (dropAction, actionWrapper) => {
        const charActionsIndex = findIndex(characterActions, {
            actionId: dropAction._id
        });
        const newCharList = without(characterActions, characterActions[charActionsIndex]);
        setCharacterActions([...newCharList]);
        const actionWrapperIndex = findIndex(character.actions, {actionId: actionWrapper.actionId});
        const newCharacter = character;
        newCharacter.actions = without(character.actions, character.actions[actionWrapperIndex]);
        setCharacter(newCharacter);
        await characterServices.updateCharacter(newCharacter);
    }

    let userFilteredActions = [];
    let userOrder;
    userFilteredActions = [...actions, ...defaultActions];
    if (userOrderDir === 'asc') {
        userOrder = 1;
    } else {
        userOrder = -1;
    }
    userFilteredActions = userFilteredActions.sort((a, b) => {
        if (
            a[userOrderBy].toLowerCase() <
            b[userOrderBy].toLowerCase()
        ) {
            return -1 * userOrder;
        } else {
            return 1 * userOrder;
        }
        }).filter(eachAction => {
            return ((userShowCustom && !eachAction.isDefault) || 
                (userShowDefault && eachAction.isDefault))    
        }).filter(eachAction => {
        return (
            eachAction['name']
            .toLowerCase()
            .includes(userQuery.toLowerCase()) ||
            eachAction['actionType']
            .toLowerCase()
            .includes(userQuery.toLowerCase()) ||
            eachAction['description']
            .toLowerCase()
            .includes(userQuery.toLowerCase())
        );
    });

    let characterFilteredActions = [];
    if(character) {
        let characterOrder;
        characterFilteredActions = character.actions.map(action => {
            const combinedAction = {
                action: find(characterActions, ['_id', action.actionId]),
                wrapper: action
            }
            return combinedAction
        });
        if (characterOrderDir === 'asc') {
            characterOrder = 1;
        } else {
            characterOrder = -1;
        }
        characterFilteredActions = characterFilteredActions.sort((a, b) => {
            if (!a.action || !b.action) {
                return false;
            }
            if (
                a.action[characterOrderBy].toLowerCase() <
                b.action[characterOrderBy].toLowerCase()
            ) {
                return -1 * characterOrder;
            } else {
                return 1 * characterOrder;
            }
            }).filter(eachAction => {
                if (!eachAction.action) {
                    return false;
                }
                return ((characterShowCustom && !eachAction.action.isDefault) || 
                    (characterShowDefault && eachAction.action.isDefault))
            }).filter(eachAction => {
            return (
                eachAction.action['name']
                .toLowerCase()
                .includes(characterQuery.toLowerCase()) ||
                eachAction.action['actionType']
                .toLowerCase()
                .includes(characterQuery.toLowerCase()) ||
                eachAction.action['description']
                .toLowerCase()
                .includes(characterQuery.toLowerCase())
            );
        })
    }

    return (
        <>
        <div className={"card-addheading bg-primary text-white card-header " + 
            (characterServices.hasCurrCharacter()? "" : "hidden-list")} onClick={toggleTopList} > 
            <div className={topHidden ? "" : "hidden-list"}>
                <FaChevronDown/> Character's Actions
            </div>
            <div className={!topHidden ? "" : "hidden-list"}>
                <FaChevronUp /> Character's Actions
            </div> 
        </div>
        <div className={"card-addheading list-holder card-header " + 
            (topHidden? "" : "hidden-list")}> 
            <div className={" list-holder " + !topHidden ? "" : "hidden-list"}>
                    <ActionWrapperForm 
                    formDisplay={characterFormDisplay}
                    toggleForm={characterToggleForm}
                    createAction={createCharacterAction}
                    updateAction={updateCharacterAction}
                    cancelForm={cancelCharacterEdit}
                    editData={editData}
                    wrapperData={wrapperData}/>
                <ActionWrapperSearch 
                    orderBy={characterOrderBy}
                    orderDir={characterOrderDir}
                    changeOrder={characterChangeOrder}
                    searchActions={characterSearch}
                    changeShow={characterChangeShow}/>
                <ActionWrapperList 
                    actions={characterFilteredActions}
                    dropAction={dropAction}
                    deleteAction={deleteAction}
                    editAction={editCharacterAction}/>
            </div>
        </div>
        <div className={"card-addheading  bg-primary text-white card-header"} onClick={toggleBottomList} > 
            <div className={bottomHidden ? "" : "hidden-list"}>
                <FaChevronDown/> All Actions
            </div>
            <div className={!bottomHidden ? "" : "hidden-list"}>
                <FaChevronUp /> All Actions
            </div> 
        </div>
        <div className={"card-addheading list-holder card-header " + 
            (bottomHidden? "" : "hidden-list")}>
            <div className={" list-holder " + !bottomHidden ? "" : "hidden-list"}>
                <ActionForm 
                    formDisplay={userFormDisplay}
                    toggleForm={userToggleForm}
                    createAction={createAction}
                    updateAction={updateAction}
                    cancelForm={cancelEdit}
                    editData={editData}/>
                <ActionSearch 
                    orderBy={userOrderBy}
                    orderDir={userOrderDir}
                    changeOrder={userChangeOrder}
                    searchActions={userSearch}
                    showCustom={userShowCustom}
                    showDefault={userShowDefault}
                    changeShow={userChangeShow}/>
                <ActionList 
                    actions={userFilteredActions} 
                    selectAction={selectAction}
                    deleteAction={deleteAction}
                    editAction={editAction}/>
            </div>
        </div>
        </>
    )
}

export default ActionPage;