export async function updateActor(actor) {
    const result = await fetch(`/api/actor/${actor._id}`, {
        method: 'put',
        body: JSON.stringify(actor),
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function submitActor(gameId, actor) {
    const result = await fetch(`/api/actor/submit/${gameId}`, {
        method: 'put',
        body: JSON.stringify(actor),
        header: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function deleteCustomActor(actorId) {
    const result = await fetch(`/api/actor/${actorId}`, {
        method: 'delete',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function deleteAllActors(userId) {
    const result = await fetch(`/api/actor/all/${userId}`, {
        method: 'delete',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
} 

