import React, {useState, useEffect} from 'react';
import { FaPlusCircle, FaCheck, FaTimesCircle} from 'react-icons/fa';

const ItemForm = ({formDisplay, toggleForm, createItem, updateItem, 
    editData, cancelForm}) => {
    const [_id, setId] = useState("");
    const [name, setName] = useState("");
    const [isDefault, setIsDefault] = useState(false);
    const [goldValue, setGoldValue] = useState("");
    const [weight, setWeight] = useState("");
    const [description, setDescription] = useState("");
    const [actionType, setActionType] = useState("Action");
    const [isConsumed, setIsConsumed] = useState(false);
    const [uses, setUses] = useState("");

    useEffect(() => {
        if(editData) {
            setId(editData._id);
            setName(editData.name);
            setGoldValue(editData.goldValue);
            setWeight(editData.weight);
            setIsDefault(editData.isDefault)
            setDescription(editData.description);
            setActionType(editData.actionType);
            setIsConsumed(editData.isConsumed);
            setUses(editData.uses);
        }
    }, [editData])


    const resetForm = () => {
        toggleForm();
        setId("");
        setName("");
        setGoldValue("");
        setWeight("");
        setIsDefault(false)
        setDescription("");
        setActionType("Action");
        setIsConsumed(false);
        setUses("")
    }

    const makeNewItem = () => {
        return {
            name: name,
            isDefault: false,
            goldValue: goldValue,
            weight: weight,
            description: description,
            actionType: actionType,
            isConsumed: isConsumed,
            uses: uses
        }
    }

    const createPressed = async (e) => {
        e.preventDefault();
        const newItem = makeNewItem();
        if (createItem) {
            newItem._id = await createItem(newItem);
        }
        resetForm();
    }
    const updatePressed = async (e) => {
        e.preventDefault();
        if (isDefault) {
            const newItem = makeNewItem();
            if (createItem) {
                newItem._id = await createItem(newItem);
            }
        } else {
            const createdItem = makeNewItem();
            createdItem._id = _id;
            if (updateItem) {
               updateItem(createdItem);
            }
        }
        resetForm();
    }
    const cancelPressed = (e) => {
        e.preventDefault();
        if (cancelForm) {
            cancelForm();
        }
        resetForm();
    }

    const updateActionType = (e, newValue) => {
        e.preventDefault();
        setActionType(newValue);
    }


    return (
        <>
        <div className={'card textcenter mt-3 ' + (formDisplay ? '' : 'add-card')}>
        <div className="card-addheading card-header  bg-primary text-white" onClick={cancelPressed}>
            <FaPlusCircle className="mr-3" />Create Item
        </div>
        <div className="card-body">
            <form className="cardForm" noValidate>

            <div className="form-group form-row">
                <label className="col-md-2 col-form-label text-md-right" htmlFor="name" readOnly>
                    Name
                </label>
                <div className="col-md-10">
                    <input type="text" className="form-control" name="name" placeholder="Item Name"
                    value={name} onChange={(e) => setName(e.target.value)}/>
                </div>
            </div>

            <div className="form-group form-row">
                <label className="col-md-2 col-form-label text-md-right" htmlFor="goldValue" readOnly>
                    Gold Value
                </label>
                <div className="col-md-10">
                    <input type="text" className="form-control" name="goldValue" placeholder="Gold Value"
                    value={goldValue} onChange={(e) => setGoldValue(e.target.value)}/>
                </div>
            </div>

            <div className="form-group form-row">
                <label className="col-md-2 col-form-label text-md-right" htmlFor="weight" readOnly>
                    Weight
                </label>
                <div className="col-md-10">
                    <input type="text" className="form-control" name="weight" placeholder="Item Weight"
                    value={weight} onChange={(e) => setWeight(e.target.value)}/>
                </div>
            </div>

            <div className="form-group form-row">
                <label className="col-md-3 col-form-label text-md-right" htmlFor="actionType" readOnly>
                    Item Action Type:
                </label>
                <button type="button" className="btn btn-primary dropdown-toggle" data-toggle="dropdown"
                    aria-haspopup="true"aria-expanded="false">
                    {actionType} <span className="caret" />
                </button>
                <div className="sort-menu dropdown-menu dropdown-menu-right">
                    <button className={'sort-by dropdown-item ' + (actionType === 'Action' ? 'active' : '')}
                        onClick={(e) => updateActionType(e, "Action")} href="#">
                        Action
                    </button>
                    <button className={'sort-by dropdown-item ' + (actionType === 'Bonus' ? 'active' : '')}
                        onClick={(e) => updateActionType(e, "Bonus")} href="#">
                        Bonus Action
                    </button>
                    <button className={'sort-by dropdown-item ' + (actionType === 'Attack' ? 'active' : '')}
                        onClick={(e) => updateActionType(e, "Attack")} href="#">
                        Attack Action
                    </button>
                    <button className={'sort-by dropdown-item ' + (actionType === 'Movement' ? 'active' : '')}
                        onClick={(e) => updateActionType(e, "Movement")} href="#">
                        Movement
                    </button>
                    <button className={'sort-by dropdown-item ' + (actionType === 'Reaction' ? 'active' : '')}
                        onClick={(e) => updateActionType(e, "Reaction")} href="#">
                        Reaction
                    </button>
                    <button className={'sort-by dropdown-item ' + (actionType === 'Object' ? 'active' : '')}
                        onClick={(e) => updateActionType(e, "Object")} href="#">
                        Object Interaction
                    </button>
                    <button className={'sort-by dropdown-item ' + (actionType === 'Free' ? 'active' : '')}
                        onClick={(e) => updateActionType(e, "Free")} href="#" >
                        Free Action
                    </button>
                </div>
            </div>

            <div className="form-check form-group form-row" onClick={() => setIsConsumed(!isConsumed)}>
                <label className="form-check-label col-sm-2" htmlFor="flexCheckChecked">
                    Is Consumed: 
                </label>
                <input className="form-check-input col-sm-3" type="checkbox"
                    checked={isConsumed} onChange={(e) => setIsConsumed(e.target.checked)}/>
            </div>

            <div className="form-group form-row">
                <label className="col-md-3 col-form-label" htmlFor="uses" readOnly>
                    Number of Uses:
                </label>
                <div className="col-md-">
                    <input type="text" className="form-control" name="uses" placeholder="Number of Uses"
                    value={uses} onChange={(e) => setUses(e.target.value)}/>
                </div>
            </div>

            <div className="form-group form-row">
                <label className="col-md-2 text-md-right" htmlFor="description">
                    Description:
                </label>
                <div className="col-md-10">
                    <textarea className="form-control" rows="4" cols="50" name="description" id="description"
                    placeholder="Action Description..." value={description} onChange={(e) => setDescription(e.target.value)}/>
                </div>
            </div>

            <div className="form-group form-row">
                <div className={"col-md-3 "+(!_id? "" : "hidden-button" )}>
                    <button type="submit" 
                    className="btn btn-primary d-block ml-auto" 
                        onClick={createPressed}>
                        Create Item <FaPlusCircle />
                    </button>
                </div>
                <div className={"col-md-3 "+(!_id ?  "hidden-button" : "")}>
                    <button type="submit" className="btn btn-primary d-block ml-auto"
                        onClick={updatePressed}>
                        Update Item <FaCheck />
                    </button>
                </div>
                <div className="col-md-2">
                    <button type="submit" className="btn btn-primary d-block ml-auto" onClick={cancelPressed}>
                        Cancel <FaTimesCircle />
                    </button>
                </div>
            </div>

            </form>
        </div>
        </div>
        </>
    )
}

export default ItemForm;