import React, {useState, useContext} from 'react';
import { FaTrashAlt, FaPencilAlt, FaTimesCircle, FaHammer } from 'react-icons/fa';
import * as gameServices from './../../../Services/game-services';
import Item from './Item';
 
const ItemWrapperList = ({items, dropItem, deleteItem, editItem, use}) => 
{
    return (
        <>
        <div className="card-list item-list mb-3">
        {items.map((item, index) => (
            <div className="card-item col media py-3" key={index}>
            <div className="card-info media-body">
                <Item item={item.item}/>
                
                <div className="row">
                    <div className="card-details col-sm-3">
                        <span className="label-item"> Is Equipped: </span>
                        {item.wrapper.active? "Yes" : "No"}
                    </div>

                    <div className="card-details col-sm-4">
                        <span className="label-item">Uses Left: </span>
                        {item.wrapper.usesLeft}
                    </div>

                    <div className="card-details col-sm-4">
                        <span className="label-item">Recovery Date: </span>
                        {item.wrapper.recoveryDate}
                    </div>
                </div>

                <div className="p-2">
                    <button onClick={() => deleteItem(item.item)} className={"col-sm-2 mx-3 card-delete btn btn-sm btn-danger " +
                        ((!item.item.isDefault && deleteItem)? "":"hidden-button")}  >
                        Delete <FaTrashAlt />
                    </button>
                    <button className={"col-sm-2 mx-3 card-delete btn btn-sm btn-warning "+(editItem? "":"hidden-button")}
                        onClick={() => editItem(item.item, item.wrapper)} >
                        Edit <FaPencilAlt />
                    </button>
                    <button className={"col-sm-2 mx-3 card-delete btn btn-sm btn-secondary "+(dropItem? "":"hidden-button")}
                        onClick={() => dropItem(item.item, item.wrapper)} >
                        Deselect  <FaTimesCircle />
                    </button>
                    <button className={"col-sm-2 mx-3 card-delete btn btn-sm btn-success "+(use? "":"hidden-button")}
                        onClick={() => use(item.item, item.wrapper)} >
                        Use <FaHammer />
                    </button>
                </div>
            </div>
        </div>
        ))}
        </div>
        </>
    );
};

export default ItemWrapperList;