import React, {useState, useEffect} from 'react';
import { FaPlusCircle, FaCheck, FaTimesCircle} from 'react-icons/fa';

const CharacterForm = ({formDisplay, toggleForm, createCharacter, updateCharacter, 
    selectCharacter, editData, cancelForm, createActor}) => {
    const [_id, setId] = useState("");
    const [name, setName] = useState("");
    const [description, setDescription] = useState("");
    const [gold, setGold] = useState("");
    const [age, setAge] = useState("");
    const [characterClass, setCharacterClass] = useState("All");
    const [characterItems, setCharacterItems] = useState([]);
    const [characterActions, setCharacterActions] = useState([]);
    const [characterStatus, setCharacterStatus] = useState([]);
    const [characterActors, setCharacterActors] = useState([]);
    
    useEffect(() => {
        if(editData) {
            setId(editData._id);
            setName(editData.name);
            setDescription(editData.description);
            setGold(editData.gold);
            setAge(editData.age);
            setCharacterClass(editData.characterClass);
            setCharacterItems(editData.items);
            setCharacterActions(editData.actions);
            setCharacterActors(editData.actors);
            setCharacterStatus(editData.status);
        }
    }, [editData])

    const resetForm = () => {
        toggleForm();
        setId("");
        setName("");
        setDescription("");
        setGold("");
        setAge("");
        setCharacterClass("");
        setCharacterItems([]);
        setCharacterActions([]);
        setCharacterActors([]);
        setCharacterStatus([]);
    }
    const createPressed = async (e) => {
        e.preventDefault();
        const newCharacter = {
            name: name,
            isDefault: false,
            description: description,
            gold: gold,
            age: age,
            characterClass: characterClass,
            items: [],
            actions: [],
            actors: [],
            status: []
        }
        if (createCharacter) {
            newCharacter._id = await createCharacter(newCharacter);
        }
        if (selectCharacter) {
            selectCharacter(newCharacter);
        }
        resetForm();
    }
    const updatePressed = (e) => {
        e.preventDefault();
        const newCharacter = {
            _id: _id,
            name: name,
            description: description,
            gold: gold,
            age: age,
            characterClass: characterClass,
            items: characterItems,
            status: characterStatus,
            actions: characterActions,
            actors: characterActors
        }
        if (updateCharacter) {
            updateCharacter(newCharacter);
        }
        resetForm();
    }
    const cancelPressed = (e) => {
        e.preventDefault();
        if (cancelForm) {
            cancelForm();
        }
        resetForm();
    }

    const updateCharacterClass = (e, newValue) => {
        e.preventDefault();
        setCharacterClass(newValue);
    }

    return (
        <>
        <div className={'card textcenter mt-3 ' + (formDisplay ? '' : 'add-card')}>
        <div className="card-addheading card-header bg-primary text-white" onClick={cancelPressed}>
            <FaPlusCircle /> Create Character
        </div>
        <div className="card-body">
            <form className="cardForm" noValidate>

            <div className="form-group form-row">
                <label className="col-md-2 col-form-label text-md-right" htmlFor="name" readOnly>
                    Name
                </label>
                <div className="col-md-10">
                    <input type="text" className="form-control" name="name" placeholder="Character Name"
                    value={name} onChange={(e) => setName(e.target.value)}/>
                </div>
            </div>

            <div className="form-group form-row">
                <label className="col-md-2 col-form-label text-md-right" htmlFor="gold" readOnly>
                    Gold
                </label>
                <div className="col-md-4">
                    <input type="number" className="form-control" name="gold" placeholder={"Character's Gold"}
                    value={gold} onChange={(e) => setGold(e.target.value)}/>
                </div>
            </div>

            <div className="form-group form-row">
                <label className="col-md-2 col-form-label text-md-right" htmlFor="age" readOnly>
                    Age In Years
                </label>
                <div className="col-md-4">
                    <input type="number" className="form-control" name="age" placeholder={"Character's Age"}
                    value={age} onChange={(e) => setAge(e.target.value)}/>
                </div>
            </div>

            <div className="btn-group col-md-4 col-form-label">
                <label className="text-md-right" htmlFor="characterClass" readOnly>
                    Character Class:
                </label>
                <span>
                <button type="button" className="btn btn-primary dropdown-toggle" data-toggle="dropdown"
                    aria-haspopup="true"aria-expanded="false" id="classToggle">
                    {characterClass} <span className="caret" />
                </button>
                <div className="sort-menu dropdown-menu dropdown-menu-right">
                    <button className={'sort-by dropdown-item ' + (characterClass === 'Artificer' ? 'active' : '')}
                        onClick={(e) => updateCharacterClass(e, "Artificer")} href="#">
                        Artificer
                    </button>
                    <button className={'sort-by dropdown-item ' + (characterClass === 'Barbarian' ? 'active' : '')}
                        onClick={(e) => updateCharacterClass(e, "Barbarian")} href="#">
                        Barbarian
                    </button>
                    <button className={'sort-by dropdown-item ' + (characterClass === 'Bard' ? 'active' : '')}
                        onClick={(e) => updateCharacterClass(e, "Bard")} href="#">
                        Bard
                    </button>
                    <button className={'sort-by dropdown-item ' + (characterClass === 'Cleric' ? 'active' : '')}
                        onClick={(e) => updateCharacterClass(e, "Cleric")} href="#">
                        Cleric
                    </button>
                    <button className={'sort-by dropdown-item ' + (characterClass === 'Druid' ? 'active' : '')}
                        onClick={(e) => updateCharacterClass(e, "Druid")} href="#">
                        Druid
                    </button>
                    <button className={'sort-by dropdown-item ' + (characterClass === 'Fighter' ? 'active' : '')}
                        onClick={(e) => updateCharacterClass(e, "Fighter")} href="#">
                        Fighter
                    </button>
                    <button className={'sort-by dropdown-item ' + (characterClass === 'Monk' ? 'active' : '')}
                        onClick={(e) => updateCharacterClass(e, "Monk")} href="#">
                        Monk
                    </button>
                    <button className={'sort-by dropdown-item ' + (characterClass === 'Paladin' ? 'active' : '')}
                        onClick={(e) => updateCharacterClass(e, "Paladin")} href="#">
                        Paladin
                    </button>
                    <button className={'sort-by dropdown-item ' + (characterClass === 'Ranger' ? 'active' : '')}
                        onClick={(e) => updateCharacterClass(e, "Ranger")} href="#">
                        Ranger
                    </button>
                    <button className={'sort-by dropdown-item ' + (characterClass === 'Sorcerer' ? 'active' : '')}
                        onClick={(e) => updateCharacterClass(e, "Sorcerer")} href="#">
                        Sorcerer
                    </button>
                    <button className={'sort-by dropdown-item ' + (characterClass === 'Warlock' ? 'active' : '')}
                        onClick={(e) => updateCharacterClass(e, "Warlock")} href="#">
                        Warlock
                    </button>
                    <button className={'sort-by dropdown-item ' + (characterClass === 'Wizard' ? 'active' : '')}
                        onClick={(e) => updateCharacterClass(e, "Wizard")} href="#">
                        Wizard
                    </button>
                    <button className={'sort-by dropdown-item ' + (characterClass === 'Caster' ? 'active' : '')}
                        onClick={(e) => updateCharacterClass(e, "Caster")} href="#">
                        Homebrew - Caster
                    </button>
                    <button className={'sort-by dropdown-item ' + (characterClass === 'Martial' ? 'active' : '')}
                        onClick={(e) => updateCharacterClass(e, "Martial")} href="#">
                        Homebrew - Martial
                    </button>
                </div>
                </span>
            </div>

            <div className="form-group form-row">
                <label className="col-md-2 text-md-right" htmlFor="description">
                    Description
                </label>
                <div className="col-md-10">
                    <textarea className="form-control" rows="4" cols="50" name="description" id="description"
                    placeholder="Character Description..." value={description} onChange={(e) => setDescription(e.target.value)}/>
                </div>
            </div>

            <div>
                Item Count: {characterItems ? characterItems.length : 0}
            </div>
            <div>
                Action Count: {characterActions ? characterActions.length: 0}
            </div>
            <div>
                Actor Count: {characterActors ? characterActors.length: 0}
            </div>
            <div>
                Status Count: {characterStatus ? characterStatus.length: 0}
            </div>

            <div className="form-group form-row mb-0 ">
                <div className={"offset-md-2 col-md-10 "+(!_id ? "" : "hidden-button" )}>
                    <button type="submit" 
                    className="btn btn-primary d-block ml-auto" onClick={createPressed}>
                        Create Character  <FaPlusCircle />
                    </button>
                </div>
                <div className={"offset-md-2 col-md-10 "+(!_id ?  "hidden-button" : "")}>
                    <button type="submit" className="btn btn-primary d-block ml-auto" onClick={updatePressed}>
                        Update Character<FaCheck />
                    </button>
                </div>
                <div className="offset-md-2 col-md-10">
                    <button type="submit" className="btn btn-primary d-block ml-auto" onClick={cancelPressed}>
                        Cancel <FaTimesCircle />
                    </button>
                </div>
            </div>

            </form>
        </div>
        </div>
        </>
    )
}

export default CharacterForm;