import React, {useContext} from 'react';
import {Link} from 'react-router-dom';
import * as userServices from '../../Services/user-services';
import * as gameServices from '../../Services/game-services';
import * as characterServices from '../../Services/character-services';
import LinkButton from '../Util/LinkButton';
import {AppContext} from '../../Services/AppContext';
import {FaDiceD20, FaHiking, FaLayerGroup, FaQuidditch, FaHorse, 
    FaMagic, FaHourglassHalf, FaInfoCircle, FaDoorOpen, FaHome} from 'react-icons/fa';

const NavBar = () => {
    const contextData = useContext(AppContext)[0];

    const [gameCharacters, setGameCharacters] = contextData.game.characters;
    const [gameActors, setGameActors] = contextData.game.actors;
    const [game, setGame] = contextData.game.game;

    const [charActions, setCharActions] = contextData.character.actions;
    const [charStatus, setCharStatus] = contextData.character.status;
    const [charItems, setCharItems] = contextData.character.items;
    const [charActors, setCharActors] = contextData.character.actors;
    const [character, setCharacter] = contextData.character.character;

    const [userActions, setUserActions] = contextData.user.actions;
    const [userStatus, setUserStatus] = contextData.user.status;
    const [userItems, setUserItems] = contextData.user.items;
    const [userActors, setUserActors] = contextData.user.actors;
    const [userCharacters, setUserCharacters] = contextData.user.characters;
    const [userGames, setUserGames] = contextData.user.games;
    const [user, setUser] = contextData.user.user;

    const signOut = () => {
        userServices.logout();
        setGameCharacters([]);
        setGameActors([]);
        setGame();
        
        setCharActions([]);
        setCharStatus([]);
        setCharItems([]);
        setCharActors([]);
        setCharacter();

        setUserActions([]);
        setUserStatus([]);
        setUserItems([]);
        setUserActors([]);
        setUserCharacters([]);
        setUserGames([]);
        setUser();
        gameServices.clearGameId();
        gameServices.clearIsHost();
        characterServices.clearCharacter();
    }

    const getNavBar = () => {
        if (userServices.isLoggedIn()) {
            return (
                <>
                <nav>
                    <ul>
                        {/* <li>
                            <Link to="/games">Games<FaDiceD20 /></Link>
                        </li> */}
                        <li>
                            <Link to="/characters">Characters    <FaHiking /></Link>
                        </li>
                        <li>
                            <label className="col-md-2 col-form-label text-md-right" htmlFor="collectionList" readOnly>
                            </label>
                            <button type="button" className="btn btn-primary dropdown-toggle" data-toggle="dropdown"
                                aria-haspopup="true"aria-expanded="false"> User Collections    <FaLayerGroup/> <span className="caret" />
                            </button>
                            <div className="sort-menu dropdown-menu dropdown-menu-right">
                                <LinkButton
                                    to='/items'
                                    className="sort-by dropdown-item "
                                    > Update Inventory    <FaQuidditch />
                                </LinkButton>
                                <LinkButton
                                    className="sort-by dropdown-item "
                                    to='/actors'
                                    > Update Creatures    <FaHorse />
                                </LinkButton>
                                <LinkButton
                                    className="sort-by dropdown-item "
                                    to='/actions'
                                    > Update Actions    <FaMagic />
                                </LinkButton>
                                <LinkButton
                                    className="sort-by dropdown-item"
                                    to='/status'
                                    > Update Status Effects    <FaHourglassHalf />
                                </LinkButton>
                            </div>
                        </li>
                        <li>
                            <Link to="/">Home    <FaHome /></Link>
                        </li>
                        <li>
                            <Link to="/about">FAQ's    <FaInfoCircle/> </Link>
                        </li>
                        <li>
                            <LinkButton
                                className="btn btn-primary"
                                to='/'
                                onClick={signOut}
                                > Sign Out    <FaDoorOpen />
                            </LinkButton>
                        </li>
                    </ul>
                </nav>
                </>
            )
        }

        return (
            <>
            <nav>
                <ul>
                    <li>
                        <Link to="/">Home<FaHome /></Link>
                    </li>
                    <li>
                        <Link to="/about">FAQ's<FaInfoCircle/></Link>
                    </li>
                    <li>
                        <Link to="/register">Register</Link>
                    </li>
                    <li>
                        <Link to="/login">Sign In</Link>
                    </li>
                </ul>
            </nav>
            </>
        )
    }

    return (
        getNavBar()
    )
}

export default NavBar;