import React, {useState, useContext} from 'react';
import {AppContext} from '../../../Services/AppContext';
import * as userServices from '../../../Services/user-services';
import LinkButton from '../../Util/LinkButton';


const Register = () => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [passConfirm, setPassConfirm] = useState('');

    const [user, setUser] = useContext(AppContext)[0].user.user;


    const updateUsername = (e) => {
        setUsername(e.target.value);
    }
    const updatePassword = (e) => {
        setPassword(e.target.value);
    }
    const updatePassConfirm = (e) => {
        setPassConfirm(e.target.value);
    }

    const submitUser = async (e) => {
        e.preventDefault();
        const regBody = await userServices.register({
            username: username,
            email: "",
            password: password
        });
        const loginBody = await userServices.login({
            username: username,
            password: password
        })
        console.log("New User Registered: ", loginBody);
        setUser(loginBody.user);
    }

    return (
        <div className={'card textcenter mt-3'}>
            <div className="card-addheading card-header bg-primary text-white">
                Register:
            </div>
        <div className="card-body">
            <form className="cardForm" noValidate onSubmit={submitUser}>

                <div className="form-group form-row">
                    <label className="col-md-2 col-form-label text-md-right" htmlFor="username" readOnly>
                    Username:
                    </label>
                    <div className="col-md-10">
                        <input className="form-control" placeholder="Username" 
                        type='text' name='username' value={username} onChange={updateUsername}/>
                    </div>
                </div>

                <div className="form-group form-row">
                    <label className="col-md-2 col-form-label text-md-right" htmlFor="password" readOnly>
                    Password:
                    </label>
                    <div className="col-md-10">
                        <input className="form-control" placeholder="Password" 
                        type='password' name='password' value={password} onChange={updatePassword}/>
                    </div>
                </div>

                <div className="form-group form-row">
                    <label className="col-md-2 col-form-label text-md-right" htmlFor="confirm" readOnly>
                    Password:
                    </label>
                    <div className="col-md-10">
                        <input className="form-control" placeholder="confirm" 
                        type='password' name='confirm' value={passConfirm} onChange={updatePassConfirm}/>
                    </div>
                </div>
                    
                <div>
                    <LinkButton
                        className="btn btn-primary"
                        to='/'
                        onClick={submitUser}
                        > Register As New User
                    </LinkButton>
                </div>
            </form>
        </div>
        </div>
    )
}

export default Register;