import React, {useContext, useState} from 'react';
import {AppContext} from '../../../Services/AppContext';
import StatusWrapperList from './../../Shared/Status/StatusWrapperList';
import StatusWrapperSearch from './../../Shared/Status/StatusWrapperSearch';
import ItemWrapperList from './../../Shared/Items/ItemWrapperList';
import ItemWrapperSearch from './../../Shared/Items/ItemWrapperSearch';
import ActionWrapperList from './../../Player/Actions/ActionWrapperList';
import ActionWrapperSearch from './../../Player/Actions/ActionWrapperSearch';
import ActorWrapperList from './../../Player/Actors/ActorWrapperList';
import ActorWrapperSearch from './../../Player/Actors/ActorWrapperSearch';
import { FaHiking, FaTrashAlt, FaHourglassHalf, FaMagic, FaHorse, FaQuidditch, FaPencilAlt, FaTimes} from 'react-icons/fa';
import LinkButton from '../../Util/LinkButton';
import { find, findIndex, without } from 'lodash';
 
const CurrentCharacter = ({dropCharacter, deleteCharacter, editCharacter, characterData}) => {
    const contextData = useContext(AppContext)[0];
    const [character, setCharacter] = contextData.character.character;
    const [currentActions, setCurrentActions] = contextData.character.actions;
    const [currentItems, setCurrentItems] = contextData.character.items;
    const [currentActors, setCurrentActors] = contextData.character.actors;
    const [currentStatus, setCurrentStatus] = contextData.character.status;

    const [itemQuery, setItemQuery] = useState('');
    const [statusQuery, setStatusQuery] = useState('');
    const [actorQuery, setActorQuery] = useState('');
    const [actionQuery, setActionQuery] = useState('');
    const itemSearch = (query) => {
        setItemQuery(query);
    }
    const statusSearch = (query) => {
        setStatusQuery(query);
    }
    const actorSearch = (query) => {
        setActorQuery(query);
    }
    const actionSearch = (query) => {
        setActionQuery(query);
    }


    const [itemOrderBy, setItemOrderBy] = useState('name');
    const [itemOrderDir, setItemOrderDir] = useState('asc');
    const [statusOrderBy, setStatusOrderBy] = useState('name');
    const [statusOrderDir, setStatusOrderDir] = useState('asc');
    const [actionOrderBy, setActorOrderBy] = useState('name');
    const [actionOrderDir, setActorOrderDir] = useState('asc');
    const [actorOrderBy, setActionOrderBy] = useState('title');
    const [actorOrderDir, setActionOrderDir] = useState('asc');
    const itemChangeOrder = (order, dir) => {
        setItemOrderBy(order);
        setItemOrderDir(dir);
    }
    const statusChangeOrder = (order, dir) => {
        setStatusOrderBy(order);
        setStatusOrderDir(dir);
    }
    const actionChangeOrder = (order, dir) => {
        setActorOrderBy(order);
        setActorOrderDir(dir);
    }
    const actorChangeOrder = (order, dir) => {
        setActionOrderBy(order);
        setActionOrderDir(dir);
    }


    const [actionShowCustom, setActionShowCustom] = useState(true);
    const [actionShowDefault, setActionShowDefault] = useState(true);
    const actionChangeShow = (showCustom, showDefault) => {
        setActionShowCustom(showCustom);
        setActionShowDefault(showDefault);
    }
    const [actorShowCustom, setActorShowCustom] = useState(true);
    const [actorShowDefault, setActorShowDefault] = useState(true);
    const [actorShowFriendly, setActorShowFriendly] = useState(true);
    const [actorShowHostile, setActorShowHostile] = useState(true);
    const [actorShowNeutral, setActorShowNeutral] = useState(true);
    const [actorShowPlayer, setActorShowPlayer] = useState(true);
    const actorChangeShow = (showCustom, showDefault,
        showFriendly, showPlayer, showHostile, showNeutral) => {
        setActorShowCustom(showCustom);
        setActorShowDefault(showDefault);
        setActorShowFriendly(showFriendly);
        setActorShowPlayer(showPlayer);
        setActorShowHostile(showHostile);
        setActorShowNeutral(showNeutral);
    }
    const [itemShowCustom, setItemShowCustom] = useState(true);
    const [itemShowDefault, setItemShowDefault] = useState(true);
    const [itemShowConsume, setItemShowConsume] = useState(true);
    const [itemShowNonConsume, setItemShowNonConsume] = useState(true);
    const [itemShowEquiped, setItemShowEquiped] = useState(true);
    const [itemShowNotEquiped, setItemShowNotEquiped] = useState(true);
    const itemChangeShow = (showCustom, showDefault, showConsume, showNonConsume, showEquiped, showNotEquiped) => {
        setItemShowCustom(showCustom);
        setItemShowDefault(showDefault);
        setItemShowConsume(showConsume);
        setItemShowNonConsume(showNonConsume);
        setItemShowEquiped(showEquiped);
        setItemShowNotEquiped(showNotEquiped);
    }
    const [statusShowCustom, setStatusShowCustom] = useState(true);
    const [statusShowDefault, setStatusShowDefault] = useState(true);
    const [statusShowExpired, setStatusShowExpired] = useState(true);
    const [statusShowActive, setStatusShowActive] = useState(true);
    const statusChangeShow = (showCustom, showDefault, showExpired, showActive) => {
        setStatusShowCustom(showCustom);
        setStatusShowDefault(showDefault);
        setStatusShowActive(showActive);
        setStatusShowExpired(showExpired);
    }
    
    let actionFilteredActions = [];
    if(character) {
        let actionOrder;
        actionFilteredActions = character.actions.map(action => {
            const combinedAction = {
                action: find(currentActions, ['_id', action.actionId]),
                wrapper: action
            }
            return combinedAction
        });
        if (actionOrderDir === 'asc') {
            actionOrder = 1;
        } else {
            actionOrder = -1;
        }
        actionFilteredActions = actionFilteredActions.sort((a, b) => {
            if (!a.action || !b.action) {
                return false;
            }
            if (
                a.action[actionOrderBy].toLowerCase() <
                b.action[actionOrderBy].toLowerCase()
            ) {
                return -1 * actionOrder;
            } else {
                return 1 * actionOrder;
            }
            }).filter(eachAction => {
                if (!eachAction.action) {
                    return false;
                }
                return ((actionShowCustom && !eachAction.action.isDefault) || 
                    (actionShowDefault && eachAction.action.isDefault))
            }).filter(eachAction => {
            return (
                eachAction.action['name']
                .toLowerCase()
                .includes(actionQuery.toLowerCase()) ||
                eachAction.action['actionType']
                .toLowerCase()
                .includes(actionQuery.toLowerCase()) ||
                eachAction.action['description']
                .toLowerCase()
                .includes(actionQuery.toLowerCase())
            );
        })
    }

    let statusFilteredStatus = [];
    if(character) {
        let statusOrder;
        statusFilteredStatus = character.status.map(status => {
            const combinedStatus = {
                status: find(currentStatus, ['_id', status.statusId]),
                wrapper: status
            }
            return combinedStatus
        });
        if (statusOrderDir === 'asc') {
            statusOrder = 1;
        } else {
            statusOrder = -1;
        }
        statusFilteredStatus = statusFilteredStatus.sort((a, b) => {
            if (!a.status || !b.status) {
                return false;
            }
            if (
                a.status[statusOrderBy].toLowerCase() <
                b.status[statusOrderBy].toLowerCase()
            ) {
                return -1 * statusOrder;
            } else {
                return 1 * statusOrder;
            }
            }).filter(eachStatus => {
                if (!eachStatus.status) {
                    return false;
                }
                return ((statusShowCustom && !eachStatus.status.isDefault) || 
                    (statusShowDefault && eachStatus.status.isDefault)) && (
                    (statusShowExpired && eachStatus.wrapper.endDate >
                        eachStatus.status.duration) ||
                    (statusShowActive && eachStatus.wrapper.endDate < 
                        eachStatus.status.duration))
            }).filter(eachStatus => {
            return (
                eachStatus.status['name']
                .toLowerCase()
                .includes(statusQuery.toLowerCase()) ||
                eachStatus.status['duration']
                .toLowerCase()
                .includes(statusQuery.toLowerCase()) ||
                eachStatus.wrapper['endDate']
                .toLowerCase()
                .includes(statusQuery.toLowerCase())
            );
        })
    }

    let itemFilteredItems = [];
    if(character) {
        let itemOrder;
        itemFilteredItems = character.items.map(item => {
            const combinedItem = {
                item: find(currentItems, ['_id', item.itemId]),
                wrapper: item
            }
            return combinedItem
        });
        if (itemOrderDir === 'asc') {
            itemOrder = 1;
        } else {
            itemOrder = -1;
        }
        itemFilteredItems = itemFilteredItems.sort((a, b) => {
            if (!a.item || !b.item) {
                return false;
            }
            if (
                a.item[itemOrderBy].toLowerCase() <
                b.item[itemOrderBy].toLowerCase()
            ) {
                return -1 * itemOrder;
            } else {
                return 1 * itemOrder;
            }
            }).filter(eachItem => {
                if (!eachItem.item) {
                    return false;
                }
                return ((itemShowCustom && !eachItem.item.isDefault) || 
                    (itemShowDefault && eachItem.item.isDefault)) &&
                    ((itemShowConsume && eachItem.item.isConsumed) ||
                    (itemShowNonConsume && !eachItem.item.isConsumed)) &&
                    ((itemShowEquiped && eachItem.wrapper.active) ||
                    (itemShowNotEquiped && !eachItem.wrapper.active))
            }).filter(eachItem => {
            return (
                eachItem.item['name']
                .toLowerCase()
                .includes(itemQuery.toLowerCase()) ||
                eachItem.item['goldValue']
                .toLowerCase()
                .includes(itemQuery.toLowerCase()) ||
                eachItem.item['weight']
                .toLowerCase()
                .includes(itemQuery.toLowerCase()) ||
                eachItem.item['actionType']
                .toLowerCase()
                .includes(itemQuery.toLowerCase())
            );
        })
    }

    let actorFilteredActors = [];
    if(character) {
        let actorOrder;
        actorFilteredActors = character.actors.map(actor => {
            const combinedActor = {
                actor: find(currentActors, ['_id', actor.actorId]),
                wrapper: actor
            }
            return combinedActor
        });
        if (actorOrderDir === 'asc') {
            actorOrder = 1;
        } else {
            actorOrder = -1;
        }
        
        actorFilteredActors = actorFilteredActors.sort((a, b) => {
            if (!a.actor || !b.actor) {
                return false;
            }
            if (
                a.actor[actorOrderBy].toLowerCase() <
                b.actor[actorOrderBy].toLowerCase()
            ) {
                return -1 * actorOrder;
            } else {
                return 1 * actorOrder;
            }
            }).filter(eachActor => {
                if (!eachActor.actor) {
                    return false;
                }
                return ((actorShowCustom && !eachActor.actor.isDefault) || 
                    (actorShowDefault && eachActor.actor.isDefault)) && (
                    (actorShowFriendly && eachActor.actor.actorType === "Friendly") ||
                    (actorShowFriendly && eachActor.actor.actorType === "Friendly") ||
                    (actorShowHostile && eachActor.actor.actorType === "Enemy") ||
                    (actorShowHostile && eachActor.actor.actorType === "LegendaryAction") ||
                    (actorShowHostile && eachActor.actor.actorType === "LegendaryEnemy") ||
                    (actorShowHostile && eachActor.actor.actorType === "Lair") ||
                    (actorShowNeutral && eachActor.actor.actorType === "NPC") ||
                    (actorShowNeutral && eachActor.actor.actorType === "Environment") ||
                    (actorShowPlayer && eachActor.actor.actorType === "PC") ||
                    (actorShowPlayer && eachActor.actor.actorType === "Pet") ||
                    (actorShowPlayer && eachActor.actor.actorType === "Companion"))
            }).filter(eachActor => {
            return (
                eachActor.actor['title']
                .toLowerCase()
                .includes(actorQuery.toLowerCase()) ||
                eachActor.actor['actorType']
                .toLowerCase()
                .includes(actorQuery.toLowerCase()) ||
                eachActor.actor['initMod']
                .toLowerCase()
                .includes(actorQuery.toLowerCase()) ||
                eachActor.actor['actionType']
                .toLowerCase()
                .includes(actorQuery.toLowerCase()) ||
                eachActor.actor['initActual']
                .toLowerCase()
                .includes(actorQuery.toLowerCase()) ||
                eachActor.actor['name']
                .toLowerCase()
                .includes(actorQuery.toLowerCase())
            );
        })
    }

    return (
        <>
        <div className="card-item col media py-3">
        <div className="card-info media-body">
            <div className="card-head d-flex">
                <span className="card-name">
                    <FaHiking />ID: {characterData ? characterData._id: ""} -- Name: {characterData ? characterData.name: ""}
                </span>
            </div>

            <div className="card-desc">
                <span className="label-item">description: </span>
                {characterData ? characterData.description: ""}
            </div>

            <div className="card-details">
                <span className="label-item">Character Class: </span>
                {characterData ? characterData.characterClass: ""}
            </div>

            <div className="card-details">
                <span className="label-item">Gold: </span>
                {characterData ? characterData.gold: ""}
            </div>

            <div className="card-details">
                <span className="label-item">Age: </span>
                {characterData ? (characterData.age): ""}
            </div>

            <ActorWrapperSearch 
                orderBy={actorOrderBy}
                orderDir={actorOrderDir}
                changeOrder={actorChangeOrder}
                searchActors={actorSearch}
                showCustom={actorShowCustom}
                showDefault={actorShowDefault}
                showFriendly={actorShowFriendly}
                showHostile={actorShowHostile}
                showNeutral={actorShowNeutral}
                showPlayer={actorShowPlayer}
                changeShow={actorChangeShow}/>
            <ActorWrapperList 
                actors={actorFilteredActors}/>
            
            <ActionWrapperSearch 
                orderBy={actionOrderBy}
                orderDir={actionOrderDir}
                changeOrder={actionChangeOrder}
                searchActions={actionSearch}
                changeShow={actionChangeShow}
                showCustom={actionShowCustom}
                showDefault={actionShowDefault}/>
            <ActionWrapperList 
                actions={actionFilteredActions}/>

            <ItemWrapperSearch 
                orderBy={itemOrderBy}
                orderDir={itemOrderDir}
                changeOrder={itemChangeOrder}
                searchItems={itemSearch}
                showCustom={itemShowCustom}
                showDefault={itemShowDefault}
                showConsume={itemShowConsume}
                showNonConsume={itemShowNonConsume}
                showEquiped={itemShowEquiped}
                showNotEquiped={itemShowNotEquiped}
                changeShow={itemChangeShow}/>
            <ItemWrapperList 
                items={itemFilteredItems}/>

            <StatusWrapperSearch 
                orderBy={statusOrderBy}
                orderDir={statusOrderDir}
                changeOrder={statusChangeOrder}
                searchStatus={statusSearch}
                showCustom={statusShowCustom}
                showDefault={statusShowDefault}
                showExpired={statusShowExpired}
                showActive={statusShowActive}
                changeShow={statusChangeShow}/>
            <StatusWrapperList 
                status={statusFilteredStatus}/>
            
            <div className="mr-3">
                <button className="card-delete btn btn-sm btn-danger" onClick={() => deleteCharacter(characterData)} >
                    Delete <FaTrashAlt />
                </button>
                <button className="card-delete btn btn-sm btn-warning" onClick={() => editCharacter(characterData)} >
                    Edit <FaPencilAlt />
                </button>
                <button className="card-delete btn btn-sm btn-secondary" onClick={() => dropCharacter(characterData)} >
                    Deselect <FaTimes /> 
                </button>
            </div>
        </div>
        </div>
        <div>
            <LinkButton
                to='/items'
                className="card-delete btn btn-sm btn-danger"
                > <FaQuidditch /> Update Inventory
            </LinkButton>
            <LinkButton
                className="card-delete btn btn-sm btn-warning"
                to='/actors'
                > <FaHorse /> Update Creatures
            </LinkButton>
            <LinkButton
                className="card-delete btn btn-sm btn-secondary"
                to='/actions'
                > <FaMagic /> Update Actions
            </LinkButton>
            <LinkButton
                className="card-delete btn btn-sm btn-secondary"
                to='/status'
                > <FaHourglassHalf /> Update Status Effects
            </LinkButton>
        </div>
        </>
    )
}

export default CurrentCharacter;