import React, {useState, useContext} from 'react';
import { FaTrashAlt, FaPencilAlt, FaTag } from 'react-icons/fa';
import * as characterServices from './../../../Services/character-services';
import * as gameServices from './../../../Services/game-services';
import Action from './Action';
 
const ActionList = ({actions, selectAction, deleteAction, editAction}) => 
{
    return (
        <>
        <div className="card-list item-list mb-3">
        {actions.map((action, index) => (
            <div className="card-item col media py-3" key={index}>
                <div className="card-info media-body">
                    <Action action={action} />
                    
                    <div className="mr-3">
                        <button onClick={() => deleteAction(action)} className={"col-sm-2 mx-3 card-delete btn btn-sm btn-danger "+
                            ((action.isDefault || gameServices.isInGame())?"hidden-button":"")} >
                            Delete <FaTrashAlt />
                        </button>
                        <button onClick={() => editAction(action)} className={"col-sm-2 mx-3 card-delete btn btn-sm btn-warning " + 
                            (gameServices.isInGame()?"hidden-button":"")}>
                            Edit <FaPencilAlt />
                        </button>
                        <button onClick={() => selectAction(action)} className={"col-sm-2 mx-3 card-delete btn btn-sm btn-primary "+
                            ((characterServices.hasCurrCharacter() && !gameServices.isInGame())?"":"hidden-button")} >
                            Select <FaTag />
                        </button>
                    </div>
                </div>
            </div>
        ))}
        </div>
        </>
    );
};

export default ActionList;