import React, {useState, useContext} from 'react';
import { FaMagic } from 'react-icons/fa';

const Action = ({action}) => {
    return (
        <>
        <div className="row py-1">
            <div className="card-head d-flex col-sm-9">
                <FaMagic className="card-icon mr-2 col-sm-2"/> 
                <span className="card-name"> Action Name: {action.name} </span>
            </div>
            
            <div className="card-details col-sm-3">
                <span className="label-item">Source: </span>
                {action.isDefault ? "Default" : "Custom"}
            </div>
        </div>

        <div className="row py-1">
            <div className="card-details col-sm-3">
                <span className="label-item">Class: </span>
                {action.class}
            </div>

            <div className="card-details col-sm-4">
                <span className="label-item">Action Type: </span>
                {action.actionType}
            </div>

            <div className="card-details col-sm-5">
                <span className="label-item">Roll Type: </span>
                {action.rollType}
            </div>
        </div>

        <div className="row py-1">
            <div className="card-details col-sm-3">
                <span className="label-item">Needs Sight: </span>
                {action.needsSight ? "Yes" : "No"}
            </div>

            <div className="card-details col-sm-4">
                <span className="label-item">Needs Sound: </span>
                {action.needsSound ? "Yes" : "No"}
            </div>

            <div className="card-details col-sm-5">
                <span className="label-item">Needs Movement: </span>
                {action.needsMovement ? "Yes" : "No"}
            </div>

            <div className="card-desc py-1">
                <span className="label-item">Description: </span>
                <div className="px-3 py-1">{action.description}</div>
            </div>
        </div>
        </>
    )
}

export default Action;