import React, {useState, useContext} from 'react';
import { FaTrashAlt, FaPencilAlt, FaTag, FaHiking } from 'react-icons/fa';
 
const CharacterList = ({characters, selectCharacter, deleteCharacter, editCharacter}) => {
    return (
        <>
        <div className="character-list item-list mb-3">
        {characters.map((item, index) => (
            <div className="card-item col media py-3" key={index}>
            <div className="card-info media-body">

                <div className="card-head d-flex">
                    <span className="card-name">
                        <FaHiking />ID: {item._id} -- Name: {item.name}
                    </span>
                </div>

                <div className="card-desc">
                    <span className="label-item">description: </span>
                    {item.description}
                </div>

                <div className="card-details">
                    <span className="label-item">Character Class: </span>
                    {item.characterClass}
                </div>

                <div className="card-details">
                    <span className="label-item">Gold: </span>
                    {item.gold}
                </div>

                <div className="card-details">
                    <span className="label-item">Age: </span>
                    {item.age}
                </div>

                <div className="mr-3">
                    <button className="card-delete btn btn-sm btn-danger" 
                        onClick={() => deleteCharacter(item)} >
                        Delete <FaTrashAlt />
                    </button>
                    <button className="card-delete btn btn-sm btn-warning" 
                        onClick={() => editCharacter(item)} >
                        Edit <FaPencilAlt />
                    </button>
                    <button className="card-delete btn btn-sm btn-primary" 
                        onClick={() => selectCharacter(item)} >
                        Select <FaTag />
                    </button>
                </div>
            </div>
            </div>
        ))}
        </div>
        </>
    );
};

export default CharacterList;