import React, {useState, useContext} from 'react';

const ActorSearch = ({orderBy, orderDir, changeOrder, searchActors, 
    showCustom, showDefault, showFriendly, showPlayer, showHostile, showNeutral, changeShow}) => {

    return (
        <>
        <div className="search-actions row justify-content-center my-4">
        <div className="col-md-12">
        <div className="input-group">
            <input type="text" className="SearchActor form-control col-md-6 text-md-right" aria-label="Search Actors"
                onChange={e => searchActors(e.target.value)}/>
            <div className="btn-group input-group-append col-md-4">
                <button type="button" className="btn btn-primary dropdown-toggle" data-toggle="dropdown"
                    aria-haspopup="true"aria-expanded="false"> 
                    Sort by: <span className="caret" />
                </button>

                <div className="sort-menu dropdown-menu dropdown-menu-right">
                    <button className={'sort-by dropdown-item ' + (orderBy === 'title' ? 'active' : '')}
                        onClick={e =>changeOrder('title', orderDir)} href="#">
                        Creature Title
                    </button>
                    <button className={'sort-by dropdown-item ' + (orderBy === 'actorType' ? 'active' : '')}
                        onClick={e => changeOrder('actorType', orderDir)} href="#">
                        Creature Type
                    </button>
                    <button className={'sort-by dropdown-item ' + (orderBy === 'initMod' ? 'active' : '')}
                        onClick={e => changeOrder('initMod', orderDir)} href="#">
                        Initiative Modifier
                    </button>
                    <div role="separator" className="dropdown-divider" />
                    <button className={'sort-by dropdown-item ' + (orderDir === 'asc' ? 'active' : '')}
                        onClick={e => changeOrder(orderBy, 'asc')} href="#">
                        Asc
                    </button>
                    <button className={'sort-by dropdown-item ' + (orderDir === 'desc' ? 'active' : '')}
                        onClick={e =>changeOrder(orderBy, 'desc')} href="#" >
                        Desc
                    </button>
                </div>
            </div>

            <div className="btn-group input-group-append col-md-4">
                <button type="button" className="btn btn-primary dropdown-toggle" data-toggle="dropdown"
                    aria-haspopup="true"aria-expanded="false"> 
                    Filter Out: <span className="caret" />
                </button>

                <div className="sort-menu dropdown-menu dropdown-menu-right">
                    <button className={'sort-by dropdown-item ' + ( showDefault ? 'active' : '')}
                        onClick={e => changeShow(showCustom, !showDefault, showFriendly, showPlayer, showHostile, showNeutral)} href="#">
                        Default Characters
                    </button>
                    <button className={'sort-by dropdown-item ' + ( showCustom ? 'active' : '')}
                        onClick={e => changeShow(!showCustom, showDefault, showFriendly, showPlayer, showHostile, showNeutral)} href="#">
                        Custom Characters
                    </button>
                    <button className={'sort-by dropdown-item ' + ( showPlayer ? 'active' : '')}
                        onClick={e => changeShow(showCustom, showDefault, showFriendly, !showPlayer, showHostile, showNeutral)} href="#">
                        Player Characters
                    </button>
                    <button className={'sort-by dropdown-item ' + ( showFriendly ? 'active' : '')}
                        onClick={e => changeShow(showCustom, showDefault, !showFriendly, showPlayer, showHostile, showNeutral)} href="#">
                        Friendly Characters
                    </button>
                    <button className={'sort-by dropdown-item ' + ( showHostile ? 'active' : '')}
                        onClick={e => changeShow(showCustom, showDefault, showFriendly, showPlayer, !showHostile, showNeutral)} href="#">
                        Hostile Characters
                    </button>
                    <button className={'sort-by dropdown-item ' + ( showNeutral ? 'active' : '')}
                        onClick={e => changeShow(showCustom, showDefault, showFriendly, showPlayer, showHostile, !showNeutral)} href="#">
                        Neutral Characters
                    </button>
                </div>
            </div>
        </div>
        </div>
        </div>
        </>
    )
}

export default ActorSearch;