import * as auth from './user-services';

export async function createStatus(status) {
    const result = await fetch(`/api/status`, {
        method: 'post',
        body: JSON.stringify(status),
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function getDefaultStatus() {
    const result = await fetch(`/api/status`, {
        method: 'get',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function getCustomStatus(userId) {
    const result = await fetch(`/api/status/custom/${userId}`, {
        method: 'get',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function getCharacterStatus(characterId) {
    const result = await fetch(`/api/status/${characterId}`, {
        method: 'get',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function updateStatus(status) {
    const result = await fetch(`/api/status/${status._id}`, {
        method: 'put',
        body: JSON.stringify(status),
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function addStatus(characterId, status) {
    const result = await fetch(`/api/status/add/${characterId}`, {
        method: 'put',
        body: JSON.stringify(status),
        header: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function deleteCustomStatus(statusId) {
    const result = await fetch(`/api/status/${statusId}`, {
        method: 'delete',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function deleteAllStatus(userId) {
    const result = await fetch(`/api/status/all/${userId}`, {
        method: 'delete',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}