import React from 'react';
import { FaQuidditch } from 'react-icons/fa';

const Item = ({item, wrapperFormat}) => {
    return (
        <>
        <div className="row py-1">
            <div className="card-head d-flex col-sm-9">
                <FaQuidditch className="card-icon mr-2 col-sm-2"/> 
                <span className="card-name"> Item Name: {item.name} </span>
            </div>
            
            <div className="card-details col-sm-3">
                <span className="label-item">Source: </span>
                {item.isDefault ? "Default" : "Custom"}
            </div>
        </div>

        <div className="row py-1">
            <div className="card-details col-sm-3">
                <span className="label-item">Weight: </span>
                {item.weight}
            </div>

            <div className="card-details col-sm-4">
                <span className="label-item">Gold Value: </span>
                {item.goldValue}
            </div>

            <div className="card-details col-sm-5">
                <span className="label-item">Action Type: </span>
                {item.actionType}
            </div>
        </div>

        <div className="row py-1">

            <div className="card-details col-sm-3">
                <span className="label-item">Item Uses: </span>
                {item.uses}
            </div>

            <div className="card-details col-sm-4">
                <span className="label-item"> Destroyed By Use: </span>
                 {item.isConsumed ? "Yes" : "No"}
            </div>
        </div>

        <div className="card-desc py-1">
            <span className="label-item">Description: </span>
            <div className="px-3 py-1">{item.description}</div>
        </div>
        </>
    )
}

export default Item