import React, {useContext} from 'react';
import {AppContext} from '../Services/AppContext';
import * as userServices from './../Services/user-services';
import * as characterServices from './../Services/character-services';
import * as gameServices from './../Services/game-services';
import * as itemServices from './../Services/item-services';
import * as actorServices from './../Services/actor-services';
import * as actionServices from './../Services/action-services';
import * as statusServices from './../Services/status-services';
import {userData} from './../data/user-data';
import {gameData} from './../data/game-data';
import {characterData} from './../data/character-data';
import {actorData, actorDefaultData} from './../data/actor-data';
import {actionData, actionDefaultData} from './../data/action-data';
import {statusData, statusDefaultData} from './../data/status-data';
import {itemData, itemDefaultData} from './../data/item-data';

const Home = () => {
    const contextData = useContext(AppContext)[0];
    const [userActions, setUserActions] = contextData.user.actions;
    const [userStatus, setUserStatus] = contextData.user.status;
    const [userItems, setUserItems] = contextData.user.items;
    const [userActors, setUserActors] = contextData.user.actors;
    const [userCharacters, setUserCharacters] = contextData.user.characters;
    const [userGames, setUserGames] = contextData.user.games;
    const [user, setUser] = contextData.user.user;

    const [defaultActions, setDefaultActions] = contextData.default.actions;
    const [defaultStatus, setDefaultStatus] = contextData.default.status;
    const [defaultActors, setDefaultActors] = contextData.default.actors;
    const [defaultItems, setDefaultItems] = contextData.default.items;

    const resetUsers = async () => {
        await userServices.deleteAllUsers(userServices.getUserId());
        await userServices.deleteUser(userServices.getUserId());
    }
    const resetGames = async () => {
        await gameServices.deleteAllGames(userServices.getUserId());
    }
    const resetCharacters = async () => {
        await characterServices.deleteAllCharacters(userServices.getUserId());
    }
    const resetItems = async () => {
        await itemServices.deleteAllItems(userServices.getUserId());
    }
    const resetStatus = async () => {
        await statusServices.deleteAllStatus(userServices.getUserId());
    }
    const resetActors = async () => {
        await actorServices.deleteAllActors(userServices.getUserId());
    }
    const resetActions = async () => {
        await actionServices.deleteAllActions(userServices.getUserId());
    }

    const populateUser = async () => {
        for (let i = 0; i < 3; i++) {
            const newUser = await userServices.register(userData[i]);
            userData[i] = newUser.user;
        }
        console.log(userData);
    }

    const populate = async () => {
        for (let i = 0; i < 3; i++) {
            for (let j = 0; j < 3; j++) {
                const newItem = await itemServices.createItem(itemData[j+i*3])
                itemData[j+i*3] =  newItem.item;
            }
            console.log(itemData);

            for (let j = 0; j < 3; j++) {
                const newActor = await actorServices.createActor(actorData[j+i*3]);
                actorData[j+i*3] = newActor.actor;
            }
            console.log(actorData);

            for (let j = 0; j < 3; j++) {
                const newAction = await actionServices.createAction(actionData[j+i*3]);
                actionData[j+i*3] = newAction.action;
            }
            console.log(actionData);

            for (let j = 0; j < 3; j++) {
                const newStatus = await statusServices.createStatus(statusData[j+i*3]);
                statusData[j+i*3] = newStatus.status;
            }
            console.log(statusData);

            characterData[i].userId = userData[i]._id;
            const newCharacter = await characterServices.createCharacter(characterData[i]);
            characterData[i] = newCharacter.character;
            console.log(characterData);

            gameData[i].userId = userData[i]._id;
            const newGame = await gameServices.createGame(gameData[i]);
            gameData[i] = newGame.game;
            
            console.log(gameData);
        }
    }

    const update = async () => {
        for (let i = 0; i < 3; i++) {
            const index = i*3;
            const index1 = i*3+1;
            const index2 = 1*3+2;
            userData[i].itemIds = [
                itemData[index]._id,
                itemData[index1]._id,
                itemData[index2]._id
            ];
            userData[i].actionIds = [
                actionData[index]._id,
                actionData[index1]._id,
                actionData[index2]._id
            ];
            userData[i].actorIds = [
                actorData[index]._id,
                actorData[index1]._id,
                actorData[index2]._id
            ];
            userData[i].statusIds = [
                statusData[index]._id,
                statusData[index1]._id,
                statusData[index2]._id
            ];
            userData[i].characterIds = [characterData[i]._id];
            userData[i].gameIds = [gameData[i]._id]
            userServices.updateUser(userData[i]);
        }
        userServices.updateUser(user);

    }

    const resetAll = () => {
        resetCharacters();
        resetGames();
        resetActions();
        resetActors();
        resetItems();
        resetStatus();
        resetUsers();
    }

    const populateDefault = async () => {
        for (let item of itemDefaultData) {
            await itemServices.createItem(item);
        }
        for (let actor of actorDefaultData) {
            await actorServices.createActor(actor);
        }
        for (let action of actionDefaultData) {
            await actionServices.createAction(action);
        }
        for (let status of statusDefaultData) {
            await statusServices.createStatus(status);
        }
    }

    const initialize = async () =>{
        await populateUser();
        await populate();
        await update();
    }

    const prod = async () => {
        await populateDefault();
    }

    return (
        <>
            <h1>Home Page</h1>

            <p>
                Basic Greeting

                Pellentesque habitant morbi tristique senectus et netus et. Dictum varius duis at consectetur lorem.
                Posuere lorem ipsum dolor sit. Nisl suscipit adipiscing bibendum est ultricies integer.
                Eu non diam phasellus vestibulum lorem sed. Amet massa vitae tortor condimentum lacinia quis vel.
                Lacus viverra vitae congue eu consequat ac felis donec. Ultrices dui sapien eget mi proin sed libero.
                Nunc consequat interdum varius sit amet mattis vulputate enim.
                Accumsan lacus vel facilisis volutpat est velit egestas dui id. Gravida cum sociis natoque penatibus et magnis dis parturient montes.
            </p>

            <h3>How This Site Works:</h3>

            <p>
                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. 
                Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.
                Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. 
                Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
            </p>

            <p>
                Cras tincidunt lobortis feugiat vivamus at augue eget. Felis imperdiet proin fermentum leo vel orci porta. 
                Etiam tempor orci eu lobortis elementum nibh tellus molestie. Fusce id velit ut tortor. Volutpat consequat mauris nunc congue nisi vitae suscipit. 
                Leo in vitae turpis massa sed. Velit laoreet id donec ultrices tincidunt arcu non sodales neque. 
                Fames ac turpis egestas maecenas pharetra convallis posuere morbi leo. Ut sem nulla pharetra diam sit. Id nibh tortor id aliquet lectus proin. 
                Eget nulla facilisi etiam dignissim diam quis enim. Sit amet nulla facilisi morbi tempus iaculis urna id volutpat.
            </p>

            <div className={userServices.getUsername() === 'admin' ? "" : "hidden-list"}>
                <button className="btn btn-secondary" onClick={prod}>Set Production Data</button>
                <button className="btn btn-warning" onClick={initialize}>Set Test Data</button>
                <button className="btn btn-danger" onClick={resetAll}>Clear Database</button>
            </div>
        </>
    )
}

export default Home;