import React from 'react';
import './css/App.css';
import {
  BrowserRouter as Router,
  Route,
  Switch,
}  from 'react-router-dom';
import Home from './Components/Home';
import AboutPage from './Components/Util/About';
import Register from './Components/Util/Registration/Register';
import Login from './Components/Util/Registration/Login';
import PageNotFound from './Components/PageNotFound';

import NavBar from './Components/Util/NavBar';
import DataLoader from './Components/Util/DataLoader';

import Items from './Components/Shared/Items/ItemPage';
import Actors from './Components/Player/Actors/ActorPage';
import Actions from './Components/Player/Actions/ActionPage';
import Status from './Components/Shared/Status/StatusPage';
import Characters from './Components/Player/Characters/CharacterPage';
// import Games from './Components/Shared/Games/GamePage';

import {AppProvider} from './Services/AppContext';

function App() {
  return (
    <AppProvider>
    <Router>
      <div className="App">
        <DataLoader />
        <NavBar />
        <div id="page-wrapper">
        <div id="page-body">
          <Switch>
            <Route path="/" component={Home} exact/>
            <Route path="/about" component={AboutPage} />
            <Route path="/register" component={Register}/>
            <Route path="/login" component={Login}/>

            <Route path="/items" component={Items}/>
            <Route path="/status" component={Status}/>
            <Route path="/actors" component={Actors}/>
            <Route path="/actions" component={Actions}/>
            <Route path="/characters" component={Characters}/>
            {/* <Route path="/games" component={Games}/> */}

            <Route component={PageNotFound} />
          </Switch>
        </div>
        </div>
      </div>
    </Router>
    </AppProvider>
  );
}

export default App;
