import React, {useState, useContext} from 'react';
import { FaTrashAlt, FaPencilAlt, FaTag } from 'react-icons/fa';
import Item from './Item';
import * as characterServices from './../../../Services/character-services';
import * as gameServices from './../../../Services/game-services';
 
const ItemList = ({items, selectItem, deleteItem, editItem}) => 
{
    return (
        <>
        <div className="card-list item-list mb-3">
        {items.map((item, index) => (
            <div className="card-item col media py-3" key={index}>
                <div className="card-info media-body card-holder">
                    <Item item={item} />
                
                    <div  className="p-2">
                        <button onClick={() => deleteItem(item)} className={"col-sm-2 mx-3 card-delete btn btn-sm btn-danger "+
                            ((item.isDefault || gameServices.isInGame())?"hidden-button":"")} >
                            Delete <FaTrashAlt />
                        </button>
                        <button onClick={() => editItem(item)} className={"col-sm-2 mx-3 card-delete btn btn-sm btn-warning " + 
                            (gameServices.isInGame()?"hidden-button":"")}>
                            Edit <FaPencilAlt />
                        </button>
                        <button onClick={() => selectItem(item)} className={"col-sm-2 mx-3 card-delete btn btn-sm btn-primary "+
                            ((characterServices.hasCurrCharacter() && !gameServices.isInGame())?"":"hidden-button")} >
                            Select <FaTag />
                        </button>
                    </div>
                </div>
            </div>
        ))}
        </div>
        </>
    );
};

export default ItemList;