import * as auth from './user-services';

export function isInGame() {
    const currGame = localStorage.getItem('game');
    return currGame != null;
}

export function getGameId () {
    return localStorage.getItem('game');
}

export function setGameId (gameId) {
    localStorage.setItem('game', gameId);
}

export function clearGameId() {
    localStorage.removeItem('game');
}

export function isHost() {
    return localStorage.getItem('host');
}

export function setIsHost(isHost) {
    localStorage.setItem('host', isHost);
}

export function clearIsHost () {
    localStorage.removeItem('host');
}

export async function createGame(game) {
    const result = await fetch(`/api/game`, {
        method: 'post',
        body: JSON.stringify(game),
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function getGames(userId) {
    const result = await fetch(`/api/game/${userId}`, {
        method: 'get',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function joinGame(joinCode) {
    const result = await fetch(`/api/game/join/${joinCode}`, {
        method: 'get',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function updateGame(game) {
    const result = await fetch(`/api/game/${game._id}`, {
        method: 'put',
        body: JSON.stringify(game),
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function deleteAllGames(userId) {
    const result = await fetch(`/api/game/all/${userId}`, {
        method: 'delete',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}