import React, {useState, createContext, useEffect} from 'react';
import * as itemServices from './item-services';
import * as statusServices from './status-services';
import * as actorServices from './actor-services';
import * as actionServices from './action-services';
import * as userServices from './user-services';
import * as gameServices from './game-services';
import * as characterServices from './character-services';

export const AppContext = createContext();

export const AppProvider = ({ children }) => {
    const [gameCharacters, setGameCharacters] = useState([]);
    const [gameActors, setGameActors] = useState([]);
    const [game, setGame] = useState();

    const [charActions, setCharActions] = useState([]);
    const [charStatus, setCharStatus] = useState([]);
    const [charItems, setCharItems] = useState([]);
    const [charActors, setCharActors] = useState([]);
    const [character, setCharacter] = useState();

    const [userActions, setUserActions] = useState([]);
    const [userStatus, setUserStatus] = useState([]);
    const [userItems, setUserItems] = useState([]);
    const [userActors, setUserActors] = useState([]);
    const [userCharacters, setUserCharacters] = useState([]);
    const [userGames, setUserGames] = useState([]);
    const [user, setUser] = useState();

    const [defaultActions, setDefaultActions] = useState([]);
    const [defaultStatus, setDefaultStatus] = useState([]);
    const [defaultItems, setDefaultItems] = useState([]);
    const [defaultActors, setDefaultActors] = useState([]);

    const appData = { 
        game: {
            characters: [gameCharacters, setGameCharacters],
            actors: [gameActors, setGameActors], 
            game: [game, setGame]
        },
        character: {
            actions: [charActions, setCharActions],
            status: [charStatus, setCharStatus],
            items: [charItems, setCharItems],
            actors: [charActors, setCharActors],
            character: [character, setCharacter]
        }, 
        user: {
            actions: [userActions, setUserActions],
            status: [userStatus, setUserStatus],
            items: [userItems, setUserItems],
            actors: [userActors, setUserActors],
            characters: [userCharacters, setUserCharacters],
            games: [userGames, setUserGames],
            user: [user, setUser]
        },
        default: {
            actions: [defaultActions, setDefaultActions],
            status: [defaultStatus, setDefaultStatus],
            items: [defaultItems, setDefaultItems],
            actors: [defaultActors, setDefaultActors]
        }
    }
    return(
        <AppContext.Provider value={[appData]}>      
            {children}
        </AppContext.Provider>
    );
}