import React, {useState, useContext} from 'react';
import {AppContext} from '../../../Services/AppContext';
import * as userServices from '../../../Services/user-services';
import * as gameServices from '../../../Services/game-services';
import * as characterServices from '../../../Services/character-services';
import * as itemServices from '../../../Services/item-services';
import * as actorServices from '../../../Services/actor-services';
import * as actionServices from '../../../Services/action-services';
import * as statusServices from '../../../Services/status-services';
import LinkButton from '../../Util/LinkButton';

const Login = () => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [loginFailed, setLoginFailed] = useState(false);

    const contextData = useContext(AppContext)[0];

    const [userActions, setUserActions] = contextData.user.actions;
    const [userStatus, setUserStatus] = contextData.user.status;
    const [userItems, setUserItems] = contextData.user.items;
    const [userActors, setUserActors] = contextData.user.actors;
    const [userCharacters, setUserCharacters] = contextData.user.characters;
    const [userGames, setUserGames] = contextData.user.games;
    const [user, setUser] = contextData.user.user;

    const updateUsername = (e) => {
        setUsername(e.target.value);
    }
    const updatePassword = (e) => {
        setPassword(e.target.value);
    }

    const submitUser = async (e) => {
        e.preventDefault();
        let loginBody = null;
        if (password.length > 0 && username.length > 0) {
            loginBody = await userServices.login({
                username: username,
                password: password
            })
        }
        if (loginBody) {
            setLoginFailed(false);
            console.log("User Logged In: ", loginBody);
            setUser(loginBody.user);
            setUserActions( (await actionServices.getCustomActions(userServices.getUserId())).actions );
            setUserActors( (await actorServices.getCustomActors(userServices.getUserId())).actors );
            setUserCharacters( (await characterServices.getCharacters(userServices.getUserId())).characters );
            setUserItems( (await itemServices.getCustomItems(userServices.getUserId())).items );
            setUserStatus( (await statusServices.getCustomStatus(userServices.getUserId())).status );
            setUserGames( (await gameServices.getGames(userServices.getUserId())).actions );
        } else {
            setLoginFailed(true);
        }
    }

    return (
        <div className={'card textcenter mt-3'}>
            <div className="card-addheading card-header bg-primary text-white">
                Login:
            </div>
        <div className="card-body">
            <form className="cardForm" noValidate onSubmit={submitUser}>

                <div className="form-group form-row">
                    <label className="col-md-2 col-form-label text-md-right" htmlFor="username" readOnly>
                    Username:
                    </label>
                    <div className="col-md-10">
                        <input className="form-control" placeholder="Username" 
                        type='text' name='username' value={username} onChange={updateUsername}/>
                    </div>
                </div>

                <div className="form-group form-row">
                    <label className="col-md-2 col-form-label text-md-right" htmlFor="password" readOnly>
                    Password:
                    </label>
                    <div className="col-md-10">
                        <input className="form-control" placeholder="Password" 
                        type='password' name='password' value={password} onChange={updatePassword}/>
                    </div>
                </div>

                <button className="btn btn-primary" onClick={submitUser}>
                    Sign In
                </button>
                <LinkButton
                    className="btn btn-primary"
                    to='/register'> 
                    I Don't Have An Account
                </LinkButton>
            </form>
        </div>
            <div className={loginFailed ? "" : "hidden-button"}>
                Login Attempt Failed
            </div>
        </div>
    )
}

export default Login;