import React, {useEffect, useContext} from 'react';
import {AppContext} from '../../Services/AppContext';
import * as itemServices from './../../Services/item-services';
import * as statusServices from './../../Services/status-services';
import * as actorServices from './../../Services/actor-services';
import * as actionServices from './../../Services/action-services';
import * as userServices from './../../Services/user-services';
import * as gameServices from './../../Services/game-services';
import * as characterServices from './../../Services/character-services';
import { find } from 'lodash';

const DataLoader = () => {
    const contextData = useContext(AppContext)[0];
    const [defaultActions, setDefaultActions] = contextData.default.actions;
    const [defaultStatus, setDefaultStatus] = contextData.default.status;
    const [defaultActors, setDefaultActors] = contextData.default.actors;
    const [defaultItems, setDefaultItems] = contextData.default.items;

    const [gameCharacters, setGameCharacters] = contextData.game.characters
    const [gameActors, setGameActors] = contextData.game.actors;
    const [game, setGame] = contextData.game.game;

    const [charActions, setCharActions] = contextData.character.actions;
    const [charStatus, setCharStatus] = contextData.character.status;
    const [charItems, setCharItems] = contextData.character.items;
    const [charActors, setCharActors] = contextData.character.actors;
    const [character, setCharacter] = contextData.character.character;

    const [userActions, setUserActions] = contextData.user.actions;
    const [userStatus, setUserStatus] = contextData.user.status;
    const [userItems, setUserItems] = contextData.user.items;
    const [userActors, setUserActors] = contextData.user.actors;
    const [userCharacters, setUserCharacters] = contextData.user.characters;
    const [userGames, setUserGames] = contextData.user.games;
    const [user, setUser] = contextData.user.user;

    useEffect(() => {
        const fetchDefaultData = async () => {
            if (userServices.isLoggedIn() && defaultActions.length === 0){
                console.log('loading default actions');
                const actions = await actionServices.getDefaultActions();
                console.log(actions.actions);
                setDefaultActions(actions.actions);
            }
            if (userServices.isLoggedIn() && defaultActors.length === 0){
                console.log('loading default actors');
                const actors = await actorServices.getDefaultActors();
                console.log(actors.actors);
                setDefaultActors(actors.actors);
            }
            if (userServices.isLoggedIn() && defaultItems.length === 0){
                console.log('loading default items');
                const items = await itemServices.getDefaultItems();
                console.log(items.items);
                setDefaultItems(items.items);
            }
            if (userServices.isLoggedIn() && defaultStatus.length === 0){
                console.log('loading default status');
                const status = await statusServices.getDefaultStatus()
                console.log(status.status);
                setDefaultStatus(status.status);
            }
        }
        fetchDefaultData();
    }, [])

    useEffect(() => {
        const fetchUserData = async () => {
            if (userServices.isLoggedIn() && !user) {
                const fetchedUser = await userServices.getUser()
                setUser(fetchedUser.user);
                const fetchedActions = await actionServices.getCustomActions(userServices.getUserId());
                setUserActions([...fetchedActions.actions]);
                const fetchedItems =  await itemServices.getCustomItems(userServices.getUserId());
                setUserItems(fetchedItems.items);
                const fetchedStatus = await statusServices.getCustomStatus(userServices.getUserId());
                setUserStatus(fetchedStatus.status);
                const fetchedActors = await actorServices.getCustomActors(userServices.getUserId());
                setUserActors(fetchedActors.actors);
                const fetchedCharacters = await characterServices.getCharacters(userServices.getUserId());
                setUserCharacters(fetchedCharacters.characters);
                const fetchedGames = await gameServices.getGames(userServices.getUserId());
                setUserGames(fetchedGames.games);
            }  
        }
        fetchUserData()
    }, [user])

    useEffect(() => {
        const fetchCharacterData = async () => {
            if (characterServices.hasCurrCharacter()) {
                setCharacter(find(userCharacters, {'_id': characterServices.getCharId()}));
                const fetchedActions = await actionServices.getCharacterActions(characterServices.getCharId());
                setCharActions(fetchedActions.actions);
                const fetchedStatus = await statusServices.getCharacterStatus(characterServices.getCharId());
                setCharStatus(fetchedStatus.status);
                const fetchedItems = await itemServices.getCharacterItems(characterServices.getCharId());
                setCharItems(fetchedItems.items);
                const fetchedActors = await actorServices.getCharacterActors(characterServices.getCharId());
                setCharActors(fetchedActors.actors);
                console.log(fetchedActors);
                fetchGameData();
            }
        }       
        fetchCharacterData();
        const fetchGameData = async () => {
            if (gameServices.isInGame()) {
                setGame(find(userGames, {'_id': gameServices.getGameId()}));
                const fetchedActors = await actorServices.getGameActors(gameServices.getGameId());
                setGameActors(fetchedActors.actors);
                const fetchedCharacters = await characterServices.getGameCharacters(gameServices.getGameId());
                setGameCharacters(fetchedCharacters.characters);
            }
        } 
        fetchGameData();
    }, [userCharacters, characterServices.hasCurrCharacter(), userGames, gameServices.isInGame()])

    useEffect(() => {
    }, [])

    return (
        <>

        </>
    )
}

export default DataLoader;