import React, {useState, useContext, useEffect} from 'react';
import {AppContext} from '../../../Services/AppContext';
import ActorList from './ActorList';
import ActorForm from './ActorForm';
import ActorSearch from './ActorSearch';
import ActorWrapperForm from './ActorWrapperForm';
import ActorWrapperList from './ActorWrapperList';
import ActorWrapperSearch from './ActorWrapperSearch';
import * as actorServices from './../../../Services/actor-services';
import * as characterServices from './../../../Services/character-services';
import * as userServices from './../../../Services/user-services';
import * as gameServices from './../../../Services/game-services';
import { find, findIndex, without } from 'lodash';
import { FaChevronDown, FaChevronUp } from 'react-icons/fa';

const ActorPage = () => {
    const contextData = useContext(AppContext)[0];
    const [actors, setActors] = contextData.user.actors;
    const [characterActors, setCharacterActors] = contextData.character.actors;
    const [defaultActors, setDefaultActors] = contextData.default.actors;

    const [user, setUser] = contextData.user.user;
    const [character, setCharacter] = contextData.character.character;

    const [userFormDisplay, setUserFormDisplay] = useState(false);
    const [characterFormDisplay, setCharacterFormDisplay] = useState(false);
    const [bottomHidden, setBottomHidden] = useState(false);
    const [topHidden, setTopHidden] = useState(false);
    const toggleTopList = () => {
        setTopHidden(!topHidden);
    }
    const toggleBottomList = () => {
        setBottomHidden(!bottomHidden);
    }
    const userToggleForm = () => {
        setUserFormDisplay(!userFormDisplay);
    }
    const characterToggleForm = () => {
        setCharacterFormDisplay(!characterFormDisplay);
    }

    const [userQuery, setUserQuery] = useState('');
    const [characterQuery, setCharacterQuery] = useState('');
    const userSearch = (query) => {
        setUserQuery(query);
    }
    const characterSearch = (query) => {
        setCharacterQuery(query);
    }

    const [userOrderBy, setUserOrderBy] = useState('title');
    const [userOrderDir, setUserOrderDir] = useState('asc');
    const [characterOrderBy, setCharacterOrderBy] = useState('title');
    const [characterOrderDir, setCharacterOrderDir] = useState('asc');
    const userChangeOrder = (order, dir) => {
        setUserOrderBy(order);
        setUserOrderDir(dir);
    }
    const characterChangeOrder = (order, dir) => {
        setCharacterOrderBy(order);
        setCharacterOrderDir(dir);
    }
    
    const [userShowCustom, setUserShowCustom] = useState(true);
    const [userShowDefault, setUserShowDefault] = useState(true);
    const [userShowFriendly, setUserShowFriendly] = useState(true);
    const [userShowHostile, setUserShowHostile] = useState(true);
    const [userShowNeutral, setUserShowNeutral] = useState(true);
    const [userShowPlayer, setUserShowPlayer] = useState(true);
    const [characterShowCustom, setCharacterShowCustom] = useState(true);
    const [characterShowDefault, setCharacterShowDefault] = useState(true);
    const [characterShowFriendly, setCharacterShowFriendly] = useState(true);
    const [characterShowHostile, setCharacterShowHostile] = useState(true);
    const [characterShowNeutral, setCharacterShowNeutral] = useState(true);
    const [characterShowPlayer, setCharacterShowPlayer] = useState(true);
    const userChangeShow = (showCustom, showDefault,
        showFriendly, showPlayer, showHostile, showNeutral) => {
        setUserShowCustom(showCustom);
        setUserShowDefault(showDefault);
        setUserShowFriendly(showFriendly);
        setUserShowPlayer(showPlayer);
        setUserShowHostile(showHostile);
        setUserShowNeutral(showNeutral);
    }
    const characterChangeShow = (showCustom, showDefault,
        showFriendly, showPlayer, showHostile, showNeutral) => {
        setCharacterShowCustom(showCustom);
        setCharacterShowDefault(showDefault);
        setCharacterShowFriendly(showFriendly);
        setCharacterShowPlayer(showPlayer);
        setCharacterShowHostile(showHostile);
        setCharacterShowNeutral(showNeutral);
    }

    const [editData, setEditData] = useState();
    const [wrapperData, setWrapperData] = useState();
    const [sendData, setSendData] = useState();
    const updateActor = async (updatedActor) => {
        let i = 0;
        for(const actor of characterActors) {
            if (actor._id == updatedActor) {
                characterActors[i] = updatedActor;
            }
            i++;
        }

        const userIndex = findIndex(actors, {
            _id: updatedActor._id
        });
        if (userIndex > -1) {
            actors[userIndex] = updatedActor;
            setActors([...actors]);
        }
        setEditData();
        await actorServices.updateActor(updatedActor);
    }
    const updateCharacterActor = async (updatedActor, updatedWrapper, originalWrapper) => {
        const userIndex = findIndex(actors, {
            _id: updatedActor._id
        });
        if (userIndex > -1) {
            actors[userIndex] = updatedActor;
            setActors([...actors]);
        }

        let i = 0;
        for(const actor of characterActors) {
            if (actor._id === updatedActor._id) {
                characterActors[i] = updatedActor;
            }
            i++;
        }
        setCharacterActors([...characterActors]);

        updateWrapper(updatedWrapper, originalWrapper);
        await actorServices.updateActor(updatedActor);
    }
    const updateWrapper = async (updatedWrapper, originalWrapper) => {
        const actorIndex = findIndex(character.actors, {
            actorId: updatedWrapper.actorId,
            isHidden: originalWrapper.isHidden,
            initActual: originalWrapper.initActual,
            name: originalWrapper.name
        });
        if (actorIndex > -1) {
            const newCharacter = character;
            newCharacter.actors[actorIndex] = updatedWrapper;
            setCharacter(newCharacter);
            await characterServices.updateCharacter(newCharacter);
        }
        setEditData();
        setWrapperData()
    }
    const cancelEdit = () => {
        setEditData();
    }
    const cancelCharacterEdit = () => {
        setEditData();
        setWrapperData()
    }
    const editActor = (actor) => {
        setEditData(actor);
        setUserFormDisplay(true);
        setCharacterFormDisplay(false);
    }
    const editCharacterActor = (actor, wrapper) => {
        setEditData(actor);
        setWrapperData(wrapper)
        setUserFormDisplay(false);
        setCharacterFormDisplay(true);
    }
    const createActor = async (createdActor) => {
        const newActor = (await actorServices.createActor(createdActor)).actor;
        setActors([...actors, newActor]);
        const newUser = user;
        newUser.actorIds.push(newActor._id);
        setUser(newUser);
        await userServices.updateUser(newUser)
        return newActor._id;
    }
    const createCharacterActor = async (newCharacterActor, newActorWrapper) => {
        const newActor = (await actorServices.createActor(newCharacterActor)).actor;
        setActors([...actors, newActor]);
        setCharacterActors([...characterActors, newActor])

        const newUser = user;
        newUser.actorIds.push(newActor._id);
        setUser(newUser);
        await userServices.updateUser(newUser)

        const newCharacter = character;
        newCharacter.actors.push({
            isHidden: newActorWrapper.isHidden,
            initActual: newActorWrapper.initActual,
            name: newActorWrapper.name,
            userId: userServices.getUserId(),
            actorId: newActor._id});
        setCharacter(newCharacter);
        await characterServices.updateCharacter(newCharacter);

        return newActor._id;
    }
    const selectActor = async (newCharacterActor) => {
        setCharacterActors([...characterActors, newCharacterActor]);

        const newCharacter = character;
        newCharacter.actors.push({
            isHidden: false,
            initActual: newCharacterActor.initRaw,
            name: newCharacterActor.title,
            userId: userServices.getUserId(),
            actorId: newCharacterActor._id});
        setCharacter(newCharacter);
        await characterServices.updateCharacter(newCharacter)
    }
    const deleteActor = async (deletedActor) => {
        if (!deletedActor.isDefault) {
                let actorWrapperIndex = -1;
                do {
                    const actorIdIndex = user.actorIds.indexOf(deletedActor._id);
                    if (actorIdIndex > -1) {
                        const newIdList = without(user.actorIds, user.actorIds[actorIdIndex]);
                        const newUser = user;
                        newUser.actorIds = newIdList;
                        setUser(newUser);
                    }
                    
                    const actorsIndex = findIndex(actors, {
                        _id: deletedActor._id
                    });
                    if (actorsIndex > -1) {
                        const newList = without(actors, actors[actorsIndex]);
                        setActors([...newList]);
                    }

                    const charActorsIndex = findIndex(characterActors, {
                        _id: deletedActor._id
                    });
                    if (charActorsIndex > -1) {
                        const newCharList = without(characterActors, characterActors[charActorsIndex]);
                        setCharacterActors([...newCharList]);
                    }
                    
                    if (character) {
                        actorWrapperIndex = findIndex(character.actors, {
                            actorId: deletedActor._id
                        });
                        if (actorWrapperIndex > -1) {
                            const newCharacter = character;
                            newCharacter.actors = without(character.actors, character.actors[actorWrapperIndex]);;
                            setCharacter(newCharacter);
                        };
                    }
                } while (actorWrapperIndex > -1)
                await userServices.updateUser(user);
                if (character) {
                    await characterServices.updateCharacter(character);
                }
                await actorServices.deleteCustomActor(deletedActor._id);
        } else {
            console.log("can't delete default");
        }
    }
    const dropActor = async (dropActor, actorWrapper) => {
        const charActorsIndex = findIndex(characterActors, {
            actorId: dropActor._id
        });
        const newCharList = without(characterActors, characterActors[charActorsIndex]);
        setCharacterActors([...newCharList]);
        const actorWrapperIndex = findIndex(character.actors, {
            isHidden: actorWrapper.isHidden,
            initActual: actorWrapper.initActual,
            name: actorWrapper.name,
            actorId: actorWrapper.actorId
        });
        const newCharacter = character;
        newCharacter.actors = without(character.actors, character.actors[actorWrapperIndex]);
        setCharacter(newCharacter);
        await characterServices.updateCharacter(newCharacter);
    }

    let userFilteredActors = [];
    let userOrder;
    userFilteredActors = [...actors, ...defaultActors];
    if (userOrderDir === 'asc') {
        userOrder = 1;
    } else {
        userOrder = -1;
    }
    userFilteredActors = userFilteredActors.sort((a, b) => {
        if (
            a[userOrderBy].toLowerCase() <
            b[userOrderBy].toLowerCase()
        ) {
            return -1 * userOrder;
        } else {
            return 1 * userOrder;
        }
        }).filter(eachActor => {
            return ((userShowCustom && !eachActor.isDefault) || 
                (userShowDefault && eachActor.isDefault)) && (
                (userShowFriendly && eachActor.actorType === "Friendly") ||
                (userShowFriendly && eachActor.actorType === "Friendly") ||
                (userShowHostile && eachActor.actorType === "Enemy") ||
                (userShowHostile && eachActor.actorType === "LegendaryAction") ||
                (userShowHostile && eachActor.actorType === "LegendaryEnemy") ||
                (userShowHostile && eachActor.actorType === "Lair") ||
                (userShowNeutral && eachActor.actorType === "NPC") ||
                (userShowNeutral && eachActor.actorType === "Environment") ||
                (userShowPlayer && eachActor.actorType === "PC") ||
                (userShowPlayer && eachActor.actorType === "Pet") ||
                (userShowPlayer && eachActor.actorType === "Companion"))     
        }).filter(eachActor => {
        return (
            eachActor['title']
            .toLowerCase()
            .includes(userQuery.toLowerCase()) ||
            eachActor['actorType']
            .toLowerCase()
            .includes(userQuery.toLowerCase()) ||
            eachActor['initMod']
            .toLowerCase()
            .includes(userQuery.toLowerCase())
        );
    });

    let characterFilteredActors = [];
    if(character) {
        let characterOrder;
        characterFilteredActors = character.actors.map(actor => {
            const combinedActor = {
                actor: find(characterActors, ['_id', actor.actorId]),
                wrapper: actor
            }
            return combinedActor
        });
        if (characterOrderDir === 'asc') {
            characterOrder = 1;
        } else {
            characterOrder = -1;
        }
        
        characterFilteredActors = characterFilteredActors.sort((a, b) => {
            if (!a.actor || !b.actor) {
                return false;
            }
            if (
                a.actor[characterOrderBy].toLowerCase() <
                b.actor[characterOrderBy].toLowerCase()
            ) {
                return -1 * characterOrder;
            } else {
                return 1 * characterOrder;
            }
            }).filter(eachActor => {
                return ((characterShowCustom && !eachActor.actor.isDefault) || 
                    (characterShowDefault && eachActor.actor.isDefault)) && (
                    (characterShowFriendly && eachActor.actor.actorType === "Friendly") ||
                    (characterShowFriendly && eachActor.actor.actorType === "Friendly") ||
                    (characterShowHostile && eachActor.actor.actorType === "Enemy") ||
                    (characterShowHostile && eachActor.actor.actorType === "LegendaryAction") ||
                    (characterShowHostile && eachActor.actor.actorType === "LegendaryEnemy") ||
                    (characterShowHostile && eachActor.actor.actorType === "Lair") ||
                    (characterShowNeutral && eachActor.actor.actorType === "NPC") ||
                    (characterShowNeutral && eachActor.actor.actorType === "Environment") ||
                    (characterShowPlayer && eachActor.actor.actorType === "PC") ||
                    (characterShowPlayer && eachActor.actor.actorType === "Pet") ||
                    (characterShowPlayer && eachActor.actor.actorType === "Companion"))
            }).filter(eachActor => {
                if (!eachActor.actor) {
                    return false;
                }
            return (
                eachActor.actor['title']
                .toLowerCase()
                .includes(characterQuery.toLowerCase()) ||
                eachActor.actor['actorType']
                .toLowerCase()
                .includes(characterQuery.toLowerCase()) ||
                eachActor.actor['initMod']
                .toLowerCase()
                .includes(characterQuery.toLowerCase()) ||
                eachActor.actor['actionType']
                .toLowerCase()
                .includes(characterQuery.toLowerCase()) ||
                eachActor.actor['initActual']
                .toLowerCase()
                .includes(characterQuery.toLowerCase()) ||
                eachActor.actor['name']
                .toLowerCase()
                .includes(characterQuery.toLowerCase())
            );
        })
    }

    return (
        <>
        <div className={"card-addheading bg-primary text-white card-header " + 
            (characterServices.hasCurrCharacter()? "" : "hidden-list")} onClick={toggleTopList} > 
            <div className={topHidden ? "" : "hidden-list"}>
                <FaChevronDown/> Character's Companions
            </div>
            <div className={!topHidden ? "" : "hidden-list"}>
                <FaChevronUp /> Character's Companions
            </div> 
        </div>
        <div className={"card-addheading list-holder card-header " + 
            (topHidden && characterServices.hasCurrCharacter()? "" : "hidden-list")}> 
            <div className={" list-holder " + !topHidden ? "" : "hidden-list"}>
                <ActorWrapperForm 
                formDisplay={characterFormDisplay}
                toggleForm={characterToggleForm}
                createActor={createCharacterActor}
                updateActor={updateCharacterActor}
                updateWrapper={updateWrapper}
                cancelForm={cancelCharacterEdit}
                editData={editData}
                wrapperData={wrapperData}/>
            <ActorWrapperSearch 
                orderBy={characterOrderBy}
                orderDir={characterOrderDir}
                changeOrder={characterChangeOrder}
                searchActors={characterSearch}
                showCustom={characterShowCustom}
                showDefault={characterShowDefault}
                showFriendly={characterShowFriendly}
                showHostile={characterShowHostile}
                showNeutral={characterShowNeutral}
                showPlayer={characterShowPlayer}
                changeShow={characterChangeShow}/>
            <ActorWrapperList 
                actors={characterFilteredActors}
                dropActor={dropActor}
                deleteActor={deleteActor}
                editActor={editCharacterActor}/>
            </div>
        </div>
        <div className={"card-addheading  bg-primary text-white card-header"} onClick={toggleBottomList} > 
            <div className={bottomHidden ? "" : "hidden-list"}>
                <FaChevronDown/> All Creatures
            </div>
            <div className={!bottomHidden ? "" : "hidden-list"}>
                <FaChevronUp /> All Creatures
            </div> 
        </div>
        <div className={"card-addheading list-holder card-header " + 
            (bottomHidden? "" : "hidden-list")}>
            <div className={" list-holder " + !bottomHidden ? "" : "hidden-list"}>
            <ActorForm 
                formDisplay={userFormDisplay}
                toggleForm={userToggleForm}
                createActor={createActor}
                updateActor={updateActor}
                cancelForm={cancelEdit}
                editData={editData}/>
            <ActorSearch 
                orderBy={userOrderBy}
                orderDir={userOrderDir}
                changeOrder={userChangeOrder}
                searchActors={userSearch}
                showCustom={userShowCustom}
                showDefault={userShowDefault}
                showFriendly={userShowFriendly}
                showHostile={userShowHostile}
                showNeutral={userShowNeutral}
                showPlayer={userShowPlayer}
                changeShow={userChangeShow}/>
            <ActorList 
                actors={userFilteredActors} 
                selectActor={selectActor}
                deleteActor={deleteActor}
                editActor={editActor}
                isTop={false}/>
            </div>
        </div>
        </>
    )
}

export default ActorPage;