import React, {useState, useContext} from 'react';
import { FaTrashAlt, FaPencilAlt, FaTimesCircle, FaHammer } from 'react-icons/fa';
import * as gameServices from './../../../Services/game-services';
import Action from './Action'
 
const ActionWrapperList = ({actions, dropAction, deleteAction, editAction, use}) => 
{
    return (
        <>
        <div className="card-list item-list mb-3">
        {actions.map((action, index) => (
            <div className="card-item col media py-3" key={index}>
                <div className="card-info media-body">
                    <Action action={action.action} />
                    
                    <div className="mr-3">
                        <button onClick={() => deleteAction(action.action)} className={"col-sm-2 mx-3 card-delete btn btn-sm btn-danger " +
                            ((!action.action.isDefault && deleteAction)? "":"hidden-button")}  >
                            Delete <FaTrashAlt />
                        </button>
                        <button className={"col-sm-2 mx-3 card-delete btn btn-sm btn-warning "+(editAction? "":"hidden-button")}
                            onClick={() => editAction(action.action, action.wrapper)} >
                            Edit <FaPencilAlt />
                        </button>
                        <button className={"col-sm-2 mx-3 card-delete btn btn-sm btn-secondary "+(dropAction? "":"hidden-button")}
                            onClick={() => dropAction(action.action, action.wrapper)} >
                            Deselect <FaTimesCircle />
                        </button>
                        <button className={"col-sm-2 mx-3 card-delete btn btn-sm btn-success "+(use? "":"hidden-button")}
                            onClick={() => use(action.action, action.wrapper)} >
                            Use Action <FaHammer />
                        </button>
                    </div>
                </div>
            </div>
        ))}
        </div>
        </>
    );
};

export default ActionWrapperList;