import * as auth from './user-services';

export async function createAction(action) {
    const result = await fetch(`/api/action`, {
        method: 'post',
        body: JSON.stringify(action),
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function getDefaultActions() {
    const result = await fetch(`/api/action`, {
        method: 'get',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function getCustomActions(userId) {
    const result = await fetch(`/api/action/custom/${userId}`, {
        method: 'get',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function getCharacterActions(characterId) {
    const result = await fetch(`/api/action/${characterId}`, {
        method: 'get',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function updateAction(action) {
    const result = await fetch(`/api/action/${action._id}`, {
        method: 'put',
        body: JSON.stringify(action),
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function addAction(characterId, action) {
    const result = await fetch(`/api/action/add/${characterId}`, {
        method: 'put',
        body: JSON.stringify(action),
        header: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function deleteCustomAction(actionId) {
    const result = await fetch(`/api/action/${actionId}`, {
        method: 'delete',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function deleteAllActions(userId) {
    const result = await fetch(`/api/action/all/${userId}`, {
        method: 'delete',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}