import React, {useState, useContext} from 'react';
import { FaHorse } from 'react-icons/fa';

const Actor = ({actor, name}) => {

    return (
        <>
        <div className="row py-1">
            <div className="card-head d-flex col-sm-9">
                <FaHorse className="card-icon mr-2 col-sm-2"/> 
                <span className="card-name"> {name? "Name: " + name + " -- " : ""} Title: {actor.title}</span>
            </div>
            
            <div className="card-details col-sm-3">
                <span className="label-item">Source: </span>
                {actor.isDefault ? "Default" : "Custom"}
            </div>
        </div>

        <div className="row py-1">
            <div className="card-details col-sm-3">
                <span className="label-item">Character Type: </span>
                {actor.actorType}
            </div>

            <div className="card-details col-sm-4">
                <span className="label-item">Initiative Modifier: </span>
                {actor.initMod}
            </div>

            <div className="card-details col-sm-5">
                <span className="label-item">Initiative Default: </span>
                {actor.initRaw}
            </div>
        </div>

        <div className="card-desc py-1">
            <span className="label-item">Description: </span>
            <div className="px-3 py-1">{actor.description}</div>
        </div>
        </>
    )
}

export default Actor;