import React, {useState, useEffect} from 'react';
import { FaPlusCircle, FaCheck, FaTimesCircle} from 'react-icons/fa';
import { without } from 'lodash';

const StatusForm = ({formDisplay, toggleForm, createStatus, updateStatus, 
    editData, cancelForm}) => {
    const [_id, setId] = useState("");
    const [name, setName] = useState("");
    const [isDefault, setIsDefault] = useState(false);
    const [description, setDescription] = useState("");
    const [duration, setDuration] = useState("");
    const [defaultConditions, setDefaultConditons] = useState(["None"]);
    const [customConditions, setCustomConditons] = useState(["None"]);

    const [newCondition, setNewCondition] = useState("");
    const [selectedCondition, setSelectedCondition] = useState("");

    useEffect(() => {
        if(editData) {
            setId(editData._id);
            setName(editData.name)
            setIsDefault(editData.isDefault)
            setDescription(editData.description);
            setDuration(editData.duration);
            setDefaultConditons(editData.defaultConditions);
            setCustomConditons(editData.customConditions);
        }
    }, [editData])

    const resetForm = () => {
        toggleForm();
        setId("");
        setName("");
        setIsDefault(false);
        setDescription("");
        setDuration("");
        setDefaultConditons(["None"]);
        setCustomConditons(["None"]);
        setNewCondition("");
        setSelectedCondition("");
    }
    const createNewStatus = () => {
        return {
            name: name,
            isDefault: false,
            description: description,
            duration: duration,
            defaultConditions: defaultConditions,
            customConditions: customConditions
        }
    }
    const createPressed = async (e) => {
        e.preventDefault();
        const newStatus = createNewStatus();
        if (createStatus) {
            newStatus._id = await createStatus(newStatus);
        }
        resetForm();
    }
    const updatePressed = async (e) => {
        e.preventDefault();
        if (isDefault) {
            const newStatus = createNewStatus();
            if (createStatus) {
                newStatus._id = await createStatus(newStatus);
            }
        } else {
            const createdStatus = createNewStatus();
            createdStatus._id = _id;
            if (updateStatus) {
               updateStatus(createdStatus);
            }
        }
        resetForm();
    }
    const cancelPressed = (e) => {
        e.preventDefault();
        if (cancelForm) {
            cancelForm();
        }
        resetForm();
    }

    const addCustomCondition = (e) => {
        e.preventDefault();
        if (customConditions.length > 0) {
            if (customConditions[0] !== "None") {
                setCustomConditons([...customConditions, newCondition]);
                setNewCondition("");
            } else {
                setCustomConditons([newCondition])
                setNewCondition("");
            }
        }
    }
    const addDefaultCondition = (e) => {
        e.preventDefault();
        if (selectedCondition.length > 0) {
            if (defaultConditions[0] != "None") {
                setDefaultConditons([...defaultConditions, selectedCondition]);
            } else {
                setDefaultConditons([selectedCondition]);
            }
        }
    }
    const removeCustomCondition = (e) => {
        e.preventDefault();
        if (customConditions.length > 0) {
            if (customConditions.length > 1) {
                customConditions.pop()
                setCustomConditons([...customConditions]);
            } else {
                setCustomConditons(["None"]);
            }
        }
    }
    const removeDefaultCondition = (e) => {
        e.preventDefault();
        if (defaultConditions.length > 0) {
            if (defaultConditions.length > 1) {
                defaultConditions.pop();
                setDefaultConditons([...defaultConditions]);
            } else {
                setDefaultConditons(["None"]);
            }
        }
    }
    const updateConditionType = (e, newValue) => {
        e.preventDefault();
        setSelectedCondition(newValue);
    }

    return (
        <>
        <div className={'card textcenter mt-3 ' + (formDisplay ? '' : 'add-card')}>
        <div className="card-addheading card-header bg-primary text-white" onClick={cancelPressed}>
            <FaPlusCircle /> Create Status
        </div>
        <div className="card-body">
            <form className="cardForm" noValidate>

            <div className="form-group form-row">
                <label className="col-md-2 col-form-label text-md-right" htmlFor="name" readOnly>
                    Status Name:
                </label>
                <div className="col-md-10">
                    <input type="text" className="form-control" name="name" placeholder="Status Name"
                    value={name} onChange={(e) => setName(e.target.value)}/>
                </div>
            </div>

            <div className="form-group form-row">
                <label className="col-md-2 col-form-label text-md-right" htmlFor="duration" readOnly>
                    Duration: 
                </label>
                <div className="col-md-10">
                    <input type="text" className="form-control" name="duration" placeholder="Duration"
                    value={duration} onChange={(e) => setDuration(e.target.value)}/>
                </div>
            </div>

            <label className="col-md-2 col-form-label text-md-right" htmlFor="selectedCondition" readOnly>
                Affects:
            </label>
            <button type="button" className="btn btn-primary dropdown-toggle" data-toggle="dropdown"
                aria-haspopup="true"aria-expanded="false">
                {selectedCondition} <span className="caret" />
            </button>
            <div className="sort-menu dropdown-menu dropdown-menu-right">
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'Movement' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "Movement")} href="#">
                    Movement
                </button>
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'Sight' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "Sight")} href="#">
                    Sight
                </button>
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'Hearing' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "Hearing")} href="#">
                    Hearing
                </button>
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'Speech' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "Speech")} href="#">
                    Speech
                </button>
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'Hands' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "Hands")} href="#">
                    Hands
                </button>
                <div role="separator" className="dropdown-divider" />
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'Action' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "Action")} href="#">
                    Action
                </button>
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'Bonus' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "Bonus")} href="#" >
                    Bonus Action
                </button>
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'Reaction' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "Reaction")} href="#" >
                    Reaction
                </button>
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'Object' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "Object")} href="#" >
                    Object Interaction
                </button>
                <div role="separator" className="dropdown-divider" />
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'Health' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "Health")} href="#" >
                    Your Hit Points
                </button>
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'Ability' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "Ability")} href="#" >
                    Your Ability Scores
                </button>
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'Armor' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "Armor")} href="#" >
                    Your Armor Class
                </button>
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'DC' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "DC")} href="#" >
                    Your Dice Check Modifiers
                </button>
                <div role="separator" className="dropdown-divider" />
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'YourAttack' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "YourAttack")} href="#" >
                    Your Attack Rolls
                </button>
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'YourSave' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "YourSave")} href="#" >
                    Your Saving Throws
                </button>
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'YourSkill' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "YourSkill")} href="#" >
                    Your Skill Checks
                </button>
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'YourContest' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "YourContest")} href="#" >
                    Your Contest Checks
                </button>
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'YourDamage' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "YourDamage")} href="#" >
                    Your Damage Rolls
                </button>
                <div role="separator" className="dropdown-divider" />
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'TheirAttack' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "TheirAttack")} href="#" >
                    Attack Rolls Against You
                </button>
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'TheirSave' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "TheirSave")} href="#" >
                    Saving Throws Against You
                </button>
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'TheirSkill' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "TheirSkill")} href="#" >
                    Skill Checks Against You
                </button>
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'TheirContest' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "TheirContest")} href="#" >
                    Contest Checks Against You
                </button>
                <button className={'sort-by dropdown-item ' + (selectedCondition === 'TheirDamage' ? 'active' : '')}
                    onClick={(e) => updateConditionType(e, "TheirDamage")} href="#" >
                    Damage Rolls Against You
                </button>
            </div>

            <div className="form-group form-row pt-3">
                <span className="label-item col-md-4 text-md-left">Default Conditions: </span>
                <div className="col-sm-4">
                    {defaultConditions.map((condition, index) => {
                        return (
                            <div key={index} className="">
                                <span className="label-item">
                                    {condition}
                                </span>
                            </div>
                        )
                    })}
                </div>
                <div className="col-sm-4 py-1">
                    <button className="btn btn-primary d-block ml-auto"
                        onClick={addDefaultCondition}>
                        Add This Condition
                    </button>
                    <button className="btn btn-danger d-block ml-auto"
                        onClick={removeDefaultCondition}>
                        Remove Condition
                    </button>
                </div>
            </div>

            <div className="form-group form-row">
                <label className="col-md-2 text-md-right" htmlFor="description">
                    Custom Condition:
                </label>
                <div className="col-md-10">
                    <textarea className="form-control" rows="4" cols="50" name="customCondition" id="customCondition"
                    placeholder="Status Description..." value={newCondition} onChange={(e) => setNewCondition(e.target.value)}/>
                </div>
            </div>

            <div className="form-group form-row pt-3">
                <span className="label-item col-md-4 text-md-left">Custom Conditions: </span>
                <div className="col-sm-4">
                {customConditions.map((condition, index) => {
                    return (
                        <div key={index} className="card-desc">
                            <span className="label-item">
                                {condition}
                            </span>
                        </div>
                    )
                })}
                </div>
                <div className="col-sm-4">
                    <button className="btn btn-primary d-block ml-auto"
                        onClick={addCustomCondition}>
                        Add Custom Condition
                    </button>
                    <button className="btn btn-danger d-block ml-auto"
                        onClick={removeCustomCondition}>
                        Remove Condition
                    </button>
                </div>
            </div>
           
            <div className="form-group form-row">
                <label className="col-md-2 text-md-right" htmlFor="description">
                    Description:
                </label>
                <div className="col-md-10">
                    <textarea className="form-control" rows="4" cols="50" name="description" id="description"
                    placeholder="Status Description..." value={description} onChange={(e) => setDescription(e.target.value)}/>
                </div>
            </div>

            <div className="form-group form-row mb-0 ">
                <div className={"offset-md-2 col-md-10 "+(!_id? "" : "hidden-button" )}>
                    <button type="submit" 
                    className="btn btn-primary d-block ml-auto" 
                        onClick={createPressed}>
                        Create Status  <FaPlusCircle />
                    </button>
                </div>
                <div className={"offset-md-2 col-md-10 "+(!_id ?  "hidden-button" : "")}>
                    <button type="submit" className="btn btn-primary d-block ml-auto"
                        onClick={updatePressed}>
                        Update Status<FaCheck />
                    </button>
                </div>
                <div className="offset-md-2 col-md-10">
                    <button type="submit" className="btn btn-primary d-block ml-auto" onClick={cancelPressed}>
                        Cancel <FaTimesCircle />
                    </button>
                </div>
            </div>

            </form>
        </div>
        </div>
        </>
    )
}

export default StatusForm;