import React, {useState, useEffect} from 'react';
import { FaPlusCircle, FaCheck, FaTimesCircle} from 'react-icons/fa';

const ActionForm = ({formDisplay, toggleForm, createAction, updateAction, 
    editData, cancelForm}) => {
    const [_id, setId] = useState("");
    const [name, setName] = useState("");
    const [isDefault, setIsDefault] = useState(false);
    const [description, setDescription] = useState("");
    const [actionType, setActionType] = useState("Action");
    const [actionClass, setActionClass] = useState("All");
    const [rollType, setRollType] = useState("None");
    const [needsSight, setNeedsSight] = useState(false);
    const [needsSound, setNeedsSound] = useState(false);
    const [needsMovement, setNeedsMovement] = useState(false);
    
    useEffect(() => {
        if(editData) {
            setId(editData._id);
            setName(editData.name);
            setIsDefault(editData.isDefault)
            setDescription(editData.description);
            setActionType(editData.actionType);
            setActionClass(editData.class);
            setRollType(editData.rollType);
            setNeedsSight(editData.needsSight);
            setNeedsSound(editData.needsSound);
            setNeedsMovement(editData.needsMovement);
        }
    }, [editData])

    const resetForm = () => {
        toggleForm();
        setId("");
        setName("");
        setIsDefault(false);
        setDescription("");
        setActionType("Action");
        setActionClass("All");
        setRollType("None");
        setNeedsSight(false);
        setNeedsSound(false);
        setNeedsMovement(false);
        setIsDefault(false);
    }

    const makeNewAction = () => {
        return {
            name: name,
            isDefault: false,
            description: description,
            class: actionClass,
            actionType: actionType,
            rollType: rollType,
            needsSight: needsSight,
            needsSound: needsSound,
            needsMovement: needsMovement
        }
    }
    const createPressed = async (e) => {
        e.preventDefault();
        const newAction = makeNewAction();
        if (createAction) {
            newAction._id = await createAction(newAction);
        }
        resetForm();
    }
    const updatePressed = async (e) => {
        e.preventDefault();
        if (isDefault) {
            const newAction = makeNewAction();
            if (createAction) {
                newAction._id = await createAction(newAction);
            }
        } else {
            const newAction = makeNewAction();
            newAction._id = _id;
            if (updateAction) {
               updateAction(newAction);
            }
        }
        resetForm();
    }
    const cancelPressed = (e) => {
        e.preventDefault();
        if (cancelForm) {
            cancelForm();
        }
        resetForm();
    }

    const updateActionType = (e, newValue) => {
        e.preventDefault();
        setActionType(newValue);
    }
    const updateClassType = (e, newValue) => {
        e.preventDefault();
        setActionClass(newValue);
    }
    const updateRollType = (e, newValue) => {
        e.preventDefault();
        setRollType(newValue);
    }


    return (
        <>
        <div className={'card textcenter mt-3 ' + (formDisplay ? '' : 'add-card')}>
        <div className="card-addheading card-header bg-primary text-white" onClick={cancelPressed}>
            <FaPlusCircle /> Create Action
        </div>
        <div className="card-body">
            <form className="cardForm" noValidate>

            <div className="form-group form-row">
                <label className="col-md-2 col-form-label text-md-right" htmlFor="name" readOnly>
                    Name
                </label>
                <div className="col-md-10">
                    <input type="text" className="form-control" name="name" placeholder="Action Name"
                    value={name} onChange={(e) => setName(e.target.value)}/>
                </div>
            </div>

            <label className="col-md-2 col-form-label text-md-right" htmlFor="actionType" readOnly>
                Action Type:
            </label>
            <button type="button" className="btn btn-primary dropdown-toggle" data-toggle="dropdown"
                aria-haspopup="true"aria-expanded="false">
                {actionType} <span className="caret" />
            </button>
            <div className="sort-menu dropdown-menu dropdown-menu-right">
                <button className={'sort-by dropdown-item ' + (actionType === 'Action' ? 'active' : '')}
                    onClick={(e) => updateActionType(e, "Action")} href="#">
                    Action
                </button>
                <button className={'sort-by dropdown-item ' + (actionType === 'Bonus' ? 'active' : '')}
                    onClick={(e) => updateActionType(e, "Bonus")} href="#">
                    Bonus Action
                </button>
                <button className={'sort-by dropdown-item ' + (actionType === 'Attack' ? 'active' : '')}
                    onClick={(e) => updateActionType(e, "Attack")} href="#">
                    Attack Action
                </button>
                <button className={'sort-by dropdown-item ' + (actionType === 'Reaction' ? 'active' : '')}
                    onClick={(e) => updateActionType(e, "Movement")} href="#">
                    Movement
                </button>
                <button className={'sort-by dropdown-item ' + (actionType === 'Movement' ? 'active' : '')}
                    onClick={(e) => updateActionType(e, "Reaction")} href="#">
                    Reaction
                </button>
                <button className={'sort-by dropdown-item ' + (actionType === 'Object' ? 'active' : '')}
                    onClick={(e) => updateActionType(e, "Object")} href="#">
                    Object Interaction
                </button>
                <button className={'sort-by dropdown-item ' + (actionType === 'Free' ? 'active' : '')}
                    onClick={(e) => updateActionType(e, "Free")} href="#" >
                    Free Action
                </button>
            </div>

            <div className="btn-group col-md-4 col-form-label">
                <label className="text-md-right" htmlFor="classType" readOnly>
                    Belongs to Class:
                </label>
                <span>
                <button type="button" className="btn btn-primary dropdown-toggle" data-toggle="dropdown"
                    aria-haspopup="true"aria-expanded="false" id="classToggle">
                    {actionClass} <span className="caret" />
                </button>
                <div className="sort-menu dropdown-menu dropdown-menu-right">
                    <button className={'sort-by dropdown-item ' + (actionClass === 'Artificer' ? 'active' : '')}
                        onClick={(e) => updateClassType(e, "Artificer")} href="#">
                        Artificer
                    </button>
                    <button className={'sort-by dropdown-item ' + (actionClass === 'Barbarian' ? 'active' : '')}
                        onClick={(e) => updateClassType(e, "Barbarian")} href="#">
                        Barbarian
                    </button>
                    <button className={'sort-by dropdown-item ' + (actionClass === 'Bard' ? 'active' : '')}
                        onClick={(e) => updateClassType(e, "Bard")} href="#">
                        Bard
                    </button>
                    <button className={'sort-by dropdown-item ' + (actionClass === 'Cleric' ? 'active' : '')}
                        onClick={(e) => updateClassType(e, "Cleric")} href="#">
                        Cleric
                    </button>
                    <button className={'sort-by dropdown-item ' + (actionClass === 'Druid' ? 'active' : '')}
                        onClick={(e) => updateClassType(e, "Druid")} href="#">
                        Druid
                    </button>
                    <button className={'sort-by dropdown-item ' + (actionClass === 'Fighter' ? 'active' : '')}
                        onClick={(e) => updateClassType(e, "Fighter")} href="#">
                        Fighter
                    </button>
                    <button className={'sort-by dropdown-item ' + (actionClass === 'Monk' ? 'active' : '')}
                        onClick={(e) => updateClassType(e, "Monk")} href="#">
                        Monk
                    </button>
                    <button className={'sort-by dropdown-item ' + (actionClass === 'Paladin' ? 'active' : '')}
                        onClick={(e) => updateClassType(e, "Paladin")} href="#">
                        Paladin
                    </button>
                    <button className={'sort-by dropdown-item ' + (actionClass === 'Ranger' ? 'active' : '')}
                        onClick={(e) => updateClassType(e, "Ranger")} href="#">
                        Ranger
                    </button>
                    <button className={'sort-by dropdown-item ' + (actionClass === 'Sorcerer' ? 'active' : '')}
                        onClick={(e) => updateClassType(e, "Sorcerer")} href="#">
                        Sorcerer
                    </button>
                    <button className={'sort-by dropdown-item ' + (actionClass === 'Warlock' ? 'active' : '')}
                        onClick={(e) => updateClassType(e, "Warlock")} href="#">
                        Warlock
                    </button>
                    <button className={'sort-by dropdown-item ' + (actionClass === 'Wizard' ? 'active' : '')}
                        onClick={(e) => updateClassType(e, "Wizard")} href="#">
                        Wizard
                    </button>
                    <div role="separator" className="dropdown-divider" />
                    <button className={'sort-by dropdown-item ' + (actionClass === 'Martial' ? 'active' : '')}
                        onClick={(e) => updateClassType(e, "Martial")} href="#">
                        Martial
                    </button>
                    <button className={'sort-by dropdown-item ' + (actionClass === 'Caster' ? 'active' : '')}
                        onClick={(e) => updateClassType(e, "Caster")} href="#">
                        Caster
                    </button>
                    <div role="separator" className="dropdown-divider" />
                    <button className={'sort-by dropdown-item ' + (actionClass === 'All' ? 'active' : '')}
                        onClick={(e) => updateClassType(e, "All")} href="#">
                        All
                    </button>
                </div>
                </span>
            </div>

            <div className="btn-group col-md-4 col-form-label">
                <label className="text-md-right" htmlFor="rollType" readOnly>
                   Roll Type:
                </label>
                <span>
                    <button type="button" className="btn btn-primary dropdown-toggle" data-toggle="dropdown"
                        aria-haspopup="true"aria-expanded="false" id="classToggle">
                        {rollType} <span className="caret" />
                    </button>
                    <div className="sort-menu dropdown-menu dropdown-menu-right">
                        <button className={'sort-by dropdown-item ' + (rollType === 'Attack' ? 'active' : '')}
                            onClick={(e) => updateRollType(e, "Attack")} href="#">
                            Attack Roll
                        </button>
                        <button className={'sort-by dropdown-item ' + (rollType === 'Save' ? 'active' : '')}
                            onClick={(e) => updateRollType(e, "Save")} href="#">
                            Saving Throw
                        </button>
                        <button className={'sort-by dropdown-item ' + (rollType === 'Contest' ? 'active' : '')}
                            onClick={(e) => updateRollType(e, "Contest")} href="#">
                            Contest Check
                        </button>
                        <button className={'sort-by dropdown-item ' + (rollType === 'Skill' ? 'active' : '')}
                            onClick={(e) => updateRollType(e, "Skill")} href="#">
                            Skill Check
                        </button>
                        <button className={'sort-by dropdown-item ' + (rollType === 'Damage' ? 'active' : '')}
                            onClick={(e) => updateRollType(e, "Damage")} href="#">
                            Damage
                        </button>
                        <button className={'sort-by dropdown-item ' + (rollType === 'Other' ? 'active' : '')}
                            onClick={(e) => updateRollType(e, "Other")} href="#">
                            Other
                        </button>
                        <button className={'sort-by dropdown-item ' + (rollType === 'None' ? 'active' : '')}
                            onClick={(e) => updateRollType(e, "None")} href="#">
                            None
                        </button>
                    </div>
                </span>
            </div>
            
            <div className="form-group form-row">
                <div className="form-check col-md-3 text-md-right" onClick={() => setNeedsSight(!needsSight)}>
                    <label className="form-check-label col-md-10" htmlFor="flexCheckChecked">
                        Needs Sight: 
                    </label>
                    <input className="form-check-input col-md-3" type="checkbox"
                        checked={needsSight} onChange={(e) => setNeedsSight(e.target.checked)}/>
                </div>
                <div className="form-check col-md-4 text-md-right" onClick={() => setNeedsSound(!needsSound)}>                
                    <label className="form-check-label col-md-10" htmlFor="flexCheckChecked">
                        Needs Sound: 
                    </label>
                    <input className="form-check-input col-md-3" type="checkbox"
                        checked={needsSound} onChange={(e) => setNeedsSound(e.target.checked)}/>
                </div>
                <div className="form-check col-md-4 text-md-right" onClick={() => setNeedsMovement(!needsMovement)}>
                    <label className="form-check-label col-md-12" htmlFor="flexCheckChecked">
                        Needs Movement: 
                    </label>
                    <input className="form-check-input col-md-3" type="checkbox"
                        checked={needsMovement}  onChange={(e) => setNeedsMovement(e.target.checked)}/>
                </div>
            </div>

            <div className="form-group form-row">
                <label className="col-md-2 text-md-right" htmlFor="description">
                    Description
                </label>
                <div className="col-md-10">
                    <textarea className="form-control" rows="4" cols="50" name="description" id="description"
                    placeholder="Action Description..." value={description} onChange={(e) => setDescription(e.target.value)}/>
                </div>
            </div>

            <div className="form-group form-row mb-0 ">
                <div className={"offset-md-2 col-md-10 "+(!_id? "" : "hidden-button" )}>
                    <button type="submit" 
                    className="btn btn-primary d-block ml-auto" 
                        onClick={createPressed}>
                        Create Action  <FaPlusCircle />
                    </button>
                </div>
                <div className={"offset-md-2 col-md-10 "+(!_id ?  "hidden-button" : "")}>
                    <button type="submit" className="btn btn-primary d-block ml-auto"
                        onClick={updatePressed}>
                        Update Action<FaCheck />
                    </button>
                </div>
                <div className="offset-md-2 col-md-10">
                    <button type="submit" className="btn btn-primary d-block ml-auto" onClick={cancelPressed}>
                        Cancel <FaTimesCircle />
                    </button>
                </div>
            </div>

            </form>
        </div>
        </div>
        </>
    )
}

export default ActionForm;