import React from 'react';
import { FaHourglassHalf } from 'react-icons/fa';

const Status = ({status}) => {
    return (
        <>
        <div className="row py-1">
            <div className="card-head d-flex col-sm-9">
                <FaHourglassHalf className="card-icon mr-2 col-sm-2"/> 
                <span className="card-name">  Status Effect Name: {status.name}</span>
            </div>
            
            <div className="card-details col-sm-3">
                <span className="label-item">Source: </span>
                {status.isDefault ? "Default" : "Custom"}
            </div>
        </div>

        <div className="row py-1">
            <div className="card-details col-sm-4">
                <span className="label-item">Default Conditions: </span>
                {status.defaultConditions.map((condition, index) => {
                    return (
                        <>
                        <div className=""></div>
                        <div key={index} className="card-desc col-sm-6">
                            {condition}
                        </div>
                        </>
                    )
                })}
            </div>

            <div className="card-details col-sm-4">
                <span className="label-item">Custom Conditions: </span>
                {status.customConditions.map((condition, index) => {
                    return (
                        <div key={index} className="card-desc">
                            {condition}
                        </div>
                    )
                })}
            </div>
            
            <div className="card-details col-sm-4">
                <span className="label-item"> Duration: </span>
                {status.duration}
            </div>
        </div>

        <div className="card-desc py-1">
            <span className="label-item">Description: </span>
            <div className="px-3 py-1">{status.description}</div>
        </div>
        </>
    )
}

export default Status

