import React, {useState, useContext, useEffect} from 'react';
import {AppContext} from '../../../Services/AppContext';
import StatusList from './StatusList';
import StatusForm from './StatusForm';
import StatusSearch from './StatusSearch';
import StatusWrapperForm from './StatusWrapperForm';
import StatusWrapperList from './StatusWrapperList';
import StatusWrapperSearch from './StatusWrapperSearch';
import * as statusServices from './../../../Services/status-services';
import * as characterServices from './../../../Services/character-services';
import * as userServices from './../../../Services/user-services';
import * as gameServices from './../../../Services/game-services';
import { find, findIndex, without } from 'lodash';
import { FaChevronDown, FaChevronUp } from 'react-icons/fa';

const StatusPage = () => {
    const contextData = useContext(AppContext)[0];
    const [status, setStatus] = contextData.user.status;
    const [characterStatus, setCharacterStatus] = contextData.character.status;
    const [defaultStatus, setDefaultStatus] = contextData.default.status;

    const [user, setUser] = contextData.user.user;
    const [character, setCharacter] = contextData.character.character;

    const [userFormDisplay, setUserFormDisplay] = useState(false);
    const [characterFormDisplay, setCharacterFormDisplay] = useState(false);
    const [bottomHidden, setBottomHidden] = useState(false);
    const [topHidden, setTopHidden] = useState(false);
    const toggleTopList = () => {
        setTopHidden(!topHidden);
    }
    const toggleBottomList = () => {
        setBottomHidden(!bottomHidden);
    }
    const userToggleForm = () => {
        setUserFormDisplay(!userFormDisplay);
    }
    const characterToggleForm = () => {
        setCharacterFormDisplay(!characterFormDisplay);
    }

    const [userQuery, setUserQuery] = useState('');
    const [characterQuery, setCharacterQuery] = useState('');
    const userSearch = (query) => {
        setUserQuery(query);
    }
    const characterSearch = (query) => {
        setCharacterQuery(query);
    }

    const [userOrderBy, setUserOrderBy] = useState('name');
    const [userOrderDir, setUserOrderDir] = useState('asc');
    const [characterOrderBy, setCharacterOrderBy] = useState('name');
    const [characterOrderDir, setCharacterOrderDir] = useState('asc');
    const userChangeOrder = (order, dir) => {
        setUserOrderBy(order);
        setUserOrderDir(dir);
    }
    const characterChangeOrder = (order, dir) => {
        setCharacterOrderBy(order);
        setCharacterOrderDir(dir);
    }
    
    const [userShowCustom, setUserShowCustom] = useState(true);
    const [userShowDefault, setUserShowDefault] = useState(true);
    const [characterShowCustom, setCharacterShowCustom] = useState(true);
    const [characterShowDefault, setCharacterShowDefault] = useState(true);
    const [characterShowExpired, setCharacterShowExpired] = useState(true);
    const [characterShowActive, setCharacterShowActive] = useState(true);
    const userChangeShow = (showCustom, showDefault) => {
        setUserShowCustom(showCustom);
        setUserShowDefault(showDefault);
    }
    const characterChangeShow = (showCustom, showDefault, showExpired, showActive) => {
        setCharacterShowCustom(showCustom);
        setCharacterShowDefault(showDefault);
        setCharacterShowActive(showActive);
        setCharacterShowExpired(showExpired);
    }

    const [editData, setEditData] = useState();
    const [wrapperData, setWrapperData] = useState();
    const [sendData, setSendData] = useState();
    const updateStatus = async (updatedStatus) => {
        let i = 0;
        for(const status of characterStatus) {
            if (status._id == updatedStatus) {
                characterStatus[i] = updatedStatus;
            }
            i++;
        }

        const userIndex = findIndex(status, {
            _id: updatedStatus._id
        });
        if (userIndex > -1) {
            status[userIndex] = updatedStatus;
            setStatus([...status]);
        }
        setEditData();
        await statusServices.updateStatus(updatedStatus);
    }
    const updateCharacterStatus = async (updatedStatus, updatedWrapper, originalWrapper) => {
        const userIndex = findIndex(status, {
            _id: updatedStatus._id
        });
        if (userIndex > -1) {
            status[userIndex] = updatedStatus;
            setStatus([...status]);
        }

        let i = 0;
        for(const status of characterStatus) {
            if (status._id === updatedStatus._id) {
                characterStatus[i] = updatedStatus;
            }
            i++;
        }
        setCharacterStatus([...characterStatus]);

        updateWrapper(updatedWrapper, originalWrapper);
        await statusServices.updateStatus(updatedStatus);
    }
    const updateWrapper = async (updatedWrapper, originalWrapper) => {
        const statusIndex = findIndex(character.status, {
            statusId: updatedWrapper.statusId,
            endDate: originalWrapper.endDate
        });
        if (statusIndex > -1) {
            const newCharacter = character;
            newCharacter.status[statusIndex] = updatedWrapper;
            setCharacter(newCharacter);
            await characterServices.updateCharacter(newCharacter);
        }
        setEditData();
        setWrapperData()
    }
    const cancelEdit = () => {
        setEditData();
    }
    const cancelCharacterEdit = () => {
        setEditData();
        setWrapperData()
    }
    const editStatus = (status) => {
        setEditData(status);
        setUserFormDisplay(true);
        setCharacterFormDisplay(false);
    }
    const editCharacterStatus = (status, wrapper) => {
        setEditData(status);
        setWrapperData(wrapper)
        setUserFormDisplay(false);
        setCharacterFormDisplay(true);
    }
    const createStatus = async (createdStatus) => {
        const newStatus = (await statusServices.createStatus(createdStatus)).status;
        setStatus([...status, newStatus]);
        const newUser = user;
        newUser.statusIds.push(newStatus._id);
        setUser(newUser);
        await userServices.updateUser(newUser)
        return newStatus._id;
    }
    const createCharacterStatus = async (newCharacterStatus, newStatusWrapper) => {
        const newStatus = (await statusServices.createStatus(newCharacterStatus)).status;
        setStatus([...status, newStatus]);
        setCharacterStatus([...characterStatus, newStatus])

        const newUser = user;
        newUser.statusIds.push(newStatus._id);
        setUser(newUser);
        await userServices.updateUser(newUser)

        const newCharacter = character;
        newCharacter.status.push({
            endDate: newStatusWrapper.endDate,
            statusId: newStatus._id});
        setCharacter(newCharacter);
        await characterServices.updateCharacter(newCharacter);

        return newStatus._id;
    }
    const selectStatus = async (newCharacterStatus) => {
        setCharacterStatus([...characterStatus, newCharacterStatus]);

        const newCharacter = character;
        newCharacter.status.push({
            endDate: newCharacterStatus.duration,
            statusId: newCharacterStatus._id});
        setCharacter(newCharacter);
        await characterServices.updateCharacter(newCharacter)
    }
    const deleteStatus = async (deletedStatus) => {
        setEditData();
        setWrapperData();
        setUserFormDisplay(false);
        setCharacterFormDisplay(false);
        if (!deletedStatus.isDefault) {
                let statusWrapperIndex = -1;
                do {
                    const statusIdIndex = user.statusIds.indexOf(deletedStatus._id);
                    if (statusIdIndex > -1) {
                        const newIdList = without(user.statusIds, user.statusIds[statusIdIndex]);
                        const newUser = user;
                        newUser.statusIds = newIdList;
                        setUser(newUser);
                    }
                    
                    const statusIndex = findIndex(status, {
                        _id: deletedStatus._id
                    });
                    if (statusIndex > -1) {
                        const newList = without(status, status[statusIndex]);
                        setStatus([...newList]);
                    }

                    const charStatusIndex = findIndex(characterStatus, {
                        _id: deletedStatus._id
                    });
                    if (charStatusIndex > -1) {
                        const newCharList = without(characterStatus, characterStatus[charStatusIndex]);
                        setCharacterStatus([...newCharList]);
                    }
                    
                    if (character) {
                        statusWrapperIndex = findIndex(character.status, {
                            statusId: deletedStatus._id
                        });
                        if (statusWrapperIndex > -1) {
                            const newCharacter = character;
                            newCharacter.status = without(character.status, character.status[statusWrapperIndex]);;
                            setCharacter(newCharacter);
                        };
                    }
                } while (statusWrapperIndex > -1)
                await userServices.updateUser(user);
                if (character) {
                    await characterServices.updateCharacter(character);
                }
                await statusServices.deleteCustomStatus(deletedStatus._id);
        } else {
            console.log("can't delete default");
        }
    }
    const dropStatus = async (dropStatus, statusWrapper) => {
        const charStatusIndex = findIndex(characterStatus, {
            statusId: dropStatus._id
        });
        const newCharList = without(characterStatus, characterStatus[charStatusIndex]);
        setCharacterStatus([...newCharList]);
        const statusWrapperIndex = findIndex(character.status, {
            endDate: statusWrapper.endDate,
            statusId: statusWrapper.statusId
        });
        const newCharacter = character;
        newCharacter.status = without(character.status, character.status[statusWrapperIndex]);
        setCharacter(newCharacter);
        await characterServices.updateCharacter(newCharacter);
    }

    let userFilteredStatus = [];
    let userOrder;
    userFilteredStatus = [...status, ...defaultStatus];
    if (userOrderDir === 'asc') {
        userOrder = 1;
    } else {
        userOrder = -1;
    }
    userFilteredStatus = userFilteredStatus.sort((a, b) => {
        if (
            a[userOrderBy].toLowerCase() <
            b[userOrderBy].toLowerCase()
        ) {
            return -1 * userOrder;
        } else {
            return 1 * userOrder;
        }
        }).filter(eachStatus => {
            return (userShowCustom && !eachStatus.isDefault) || 
                (userShowDefault && eachStatus.isDefault)     
        }).filter(eachStatus => {
        return (
            eachStatus['name']
            .toLowerCase()
            .includes(userQuery.toLowerCase()) ||
            eachStatus['duration']
            .toLowerCase()
            .includes(userQuery.toLowerCase())
        );
    });

    let characterFilteredStatus = [];
    if(character) {
        let characterOrder;
        characterFilteredStatus = character.status.map(status => {
            const combinedStatus = {
                status: find(characterStatus, ['_id', status.statusId]),
                wrapper: status
            }
            return combinedStatus
        });
        if (characterOrderDir === 'asc') {
            characterOrder = 1;
        } else {
            characterOrder = -1;
        }
        characterFilteredStatus = characterFilteredStatus.sort((a, b) => {
            if (
                a.status[characterOrderBy].toLowerCase() <
                b.status[characterOrderBy].toLowerCase()
            ) {
                return -1 * characterOrder;
            } else {
                return 1 * characterOrder;
            }
            }).filter(eachStatus => {
                return ((characterShowCustom && !eachStatus.status.isDefault) || 
                    (characterShowDefault && eachStatus.status.isDefault)) && (
                    (characterShowExpired && eachStatus.wrapper.endDate >
                        eachStatus.status.duration) ||
                    (characterShowActive && eachStatus.wrapper.endDate < 
                        eachStatus.status.duration))
            }).filter(eachStatus => {
            return (
                eachStatus.status['name']
                .toLowerCase()
                .includes(characterQuery.toLowerCase()) ||
                eachStatus.status['duration']
                .toLowerCase()
                .includes(characterQuery.toLowerCase()) ||
                eachStatus.wrapper['endDate']
                .toLowerCase()
                .includes(characterQuery.toLowerCase())
            );
        })
    }

    return (
        <>
        <div className={"card-addheading bg-primary text-white card-header " + 
            (characterServices.hasCurrCharacter()? "" : "hidden-list")} onClick={toggleTopList} > 
            <div className={topHidden ? "" : "hidden-list"}>
                <FaChevronDown/> Character's Status Effects
            </div>
            <div className={!topHidden ? "" : "hidden-list"}>
                <FaChevronUp /> Character's Status Effects
            </div> 
        </div>
        <div className={"card-addheading list-holder card-header " + 
            (topHidden? "" : "hidden-list")}> 
            <div className={" list-holder " + !topHidden ? "" : "hidden-list"}>
            <StatusWrapperForm 
                formDisplay={characterFormDisplay}
                toggleForm={characterToggleForm}
                createStatus={createCharacterStatus}
                updateStatus={updateCharacterStatus}
                updateWrapper={updateWrapper}
                cancelForm={cancelCharacterEdit}
                editData={editData}
                wrapperData={wrapperData}/>
            <StatusWrapperSearch 
                orderBy={characterOrderBy}
                orderDir={characterOrderDir}
                changeOrder={characterChangeOrder}
                searchStatus={characterSearch}
                showCustom={characterShowCustom}
                showDefault={characterShowDefault}
                showExpired={characterShowExpired}
                showActive={characterShowActive}
                changeShow={characterChangeShow}/>
            <StatusWrapperList 
                status={characterFilteredStatus}
                dropStatus={dropStatus}
                deleteStatus={deleteStatus}
                editStatus={editCharacterStatus}/>
        </div>
    </div>
    <div className={"card-addheading  bg-primary text-white card-header"} onClick={toggleBottomList} > 
        <div className={bottomHidden ? "" : "hidden-list"}>
            <FaChevronDown/> All Status Effects
        </div>
        <div className={!bottomHidden ? "" : "hidden-list"}>
            <FaChevronUp /> All Status Effects
        </div> 
    </div>
    <div className={"card-addheading list-holder card-header " + 
        (bottomHidden? "" : "hidden-list")}>
        <div className={" list-holder " + !bottomHidden ? "" : "hidden-list"}>
            <StatusForm 
                formDisplay={userFormDisplay}
                toggleForm={userToggleForm}
                createStatus={createStatus}
                updateStatus={updateStatus}
                cancelForm={cancelEdit}
                editData={editData}/>
            <StatusSearch 
                orderBy={userOrderBy}
                orderDir={userOrderDir}
                changeOrder={userChangeOrder}
                searchStatus={userSearch}
                showCustom={userShowCustom}
                showDefault={userShowDefault}
                changeShow={userChangeShow}/>
            <StatusList 
                status={userFilteredStatus} 
                selectStatus={selectStatus}
                deleteStatus={deleteStatus}
                editStatus={editStatus}
                isTop={false}/>
            </div>
        </div>
        </>
    )
}

export default StatusPage;