import React, {useState, useContext, useEffect} from 'react';
import {AppContext} from '../../../Services/AppContext';
import CharacterList from './CharacterList';
import CharacterForm from './CharacterForm';
import CharacterSearch from './CharacterSearch';
import CurrentCharacter from './CurrentCharacter';
import CurrentCharacterForm from './CurrentCharacterForm';
import * as characterServices from './../../../Services/character-services';
import * as userServices from './../../../Services/user-services';
import * as gameServices from './../../../Services/game-services';
import { filter, findIndex, without } from 'lodash';
import { FaChevronDown, FaChevronUp } from 'react-icons/fa';

const CharacterPage = () => {
    const contextData = useContext(AppContext)[0];
    const [currentActions, setCurrentActions] = contextData.character.actions;
    const [currentItems, setCurrentItems] = contextData.character.items;
    const [currentActors, setCurrentActors] = contextData.character.actors;
    const [currentStatus, setCurrentStatus] = contextData.character.status;
    const [userActions, setUserActions] = contextData.user.actions;
    const [userItems, setUserItems] = contextData.user.items;
    const [userActors, setUserActors] = contextData.user.actors;
    const [userStatus, setUserStatus] = contextData.user.status;
    const [defaultActions, setDefaultActions] = contextData.default.actions;
    const [defaultItems, setDefaultItems] = contextData.default.items;
    const [defaultActors, setDefaultActors] = contextData.default.actors;
    const [defaultStatus, setDefaultStatus] = contextData.default.status;

    const [characters, setCharacters] = contextData.user.characters;
    const [currentCharacter, setCurrentCharacter] = contextData.character.character;
    const [user, setUser] = contextData.user.user;

    const [userFormDisplay, setUserFormDisplay] = useState(false);
    const [currentFormDisplay, setCurrentFormDisplay] = useState(false);
    const [bottomHidden, setBottomHidden] = useState(false);
    const [topHidden, setTopHidden] = useState(false);
    const toggleTopList = () => {
        setTopHidden(!topHidden);
    }
    const toggleBottomList = () => {
        setBottomHidden(!bottomHidden);
    }
    const userToggleForm = () => {
        setUserFormDisplay(!userFormDisplay);
    }
    const currentToggleForm = () => {
        setCurrentFormDisplay(!currentFormDisplay);
    }

    const [userQuery, setUserQuery] = useState('');
    const [userOrderBy, setUserOrderBy] = useState('name');
    const [userOrderDir, setUserOrderDir] = useState('asc');
    const userSearch = (query) => {
        setUserQuery(query);
    }
    const userChangeOrder = (order, dir) => {
        setUserOrderBy(order);
        setUserOrderDir(dir);
    }

    const [editData, setEditData] = useState();
    const updateCurrentCharacter = async (updatedCharacter) => {
        const userIndex = findIndex(characters, {
            _id: updatedCharacter._id
        });
        if (userIndex > -1) {
            characters[userIndex] = updatedCharacter;
            setCharacters([...characters]);
        }
        setCurrentCharacter(updatedCharacter);
        await characterServices.updateCharacter(updatedCharacter);
    }
    const updateCharacter = async (updatedCharacter) => {
        if (currentCharacter && currentCharacter._id === updatedCharacter._id) {
            setCurrentCharacter(updatedCharacter);
        }
        const userIndex = findIndex(characters, {
            _id: updatedCharacter._id
        });
        if (userIndex > -1) {
            characters[userIndex] = updatedCharacter;
            setCharacters([...characters]);
        }
        setEditData();
        await characterServices.updateCharacter(updatedCharacter);
    }
    const cancelEdit = () => {
        setEditData();
    }
    const editCharacter = (character) => {
        setEditData(character);
        setUserFormDisplay(true);
    }
    const editCurrentCharacter = () => {
        setCurrentFormDisplay(true);
    }
    const createCharacter = async (character) => {
        character.username = userServices.getUsername();
        const newCharacter = (await characterServices.createCharacter(character)).character;
        setCharacters([...characters, newCharacter]);
        const newUser = user;
        newUser.characterIds.push(newCharacter._id);
        setUser(newUser);
        await userServices.updateUser(newUser);
        return newCharacter._id;
    }
    const selectCharacter = (newCurrentCharacter) => {
        setCurrentCharacter(newCurrentCharacter);
        const newActions = [];
        for (let action of newCurrentCharacter.actions){
            newActions.push(filter([...userActions, ...defaultActions], (o) => {
                return o._id == action.actionId;
            }));
        }
        const newActors = [];
        for (let actor of newCurrentCharacter.actors){
            newActors.push(filter([...userActors, ...defaultActors], (o) => {
                return o._id == actor.actorId;
            }));
        }
        const newItems = [];
        for (let item of newCurrentCharacter.items){
            newItems.push(filter([...userItems, ...defaultItems], (o) => {
                return o._id == item.itemId;
            }));
        }
        const newStatus = [];
        for (let status of newCurrentCharacter.status){
            newStatus.push(filter([...userStatus, ...defaultStatus], (o) => {
                return o._id == status.statusId;
            }));
        }
        setCurrentActions(newActions);
        setCurrentStatus(newStatus);
        setCurrentItems(newItems);
        setCurrentActors(newActors);
        characterServices.setCharId(newCurrentCharacter._id);
    }
    const deleteCharacter = async (deletedCharacter) => {
        const newList = without(characters, deletedCharacter);
        const newIdList = without(user.characterIds, deletedCharacter._id);
        const newUser = user;
        newUser.characterIds = newIdList;
        setUser(newUser);
        setCharacters([...newList]);
        if (currentCharacter && deletedCharacter._id === currentCharacter._id) {
            dropCharacter();
        }
        await userServices.updateUser(newUser);
        await characterServices.deleteCharacter(deletedCharacter._id);
    }
    const dropCharacter = async () => {
        setCurrentCharacter();
        setCurrentActions([]);
        setCurrentActors([]);
        setCurrentItems([]);
        setCurrentStatus([]);
        setCurrentFormDisplay(false);
        characterServices.clearCharacter();
    }

    let userFilteredCharacters = [];
    let userOrder;
    userFilteredCharacters = [...characters];
    if (userOrderDir === 'asc') {
        userOrder = 1;
    } else {
        userOrder = -1;
    }
    userFilteredCharacters = userFilteredCharacters.sort((a, b) => {
        if (
            a[userOrderBy].toLowerCase() <
            b[userOrderBy].toLowerCase()
        ) {
            return -1 * userOrder;
        } else {
            return 1 * userOrder;
        }
        }).filter(eachItem => {
        return (
            eachItem['name']
            .toLowerCase()
            .includes(userQuery.toLowerCase()) || 
            eachItem['characterClass']
            .toLowerCase()
            .includes(userQuery.toLowerCase())
        );
    });

    return (
        <>
        <div className={"card-addheading bg-primary text-white card-header " + 
            (characterServices.hasCurrCharacter()? "" : "hidden-list")} onClick={toggleTopList} > 
            <div className={topHidden ? "" : "hidden-list"}>
                <FaChevronDown/> Character's Inventory
            </div>
            <div className={!topHidden ? "" : "hidden-list"}>
                <FaChevronUp /> Character's Inventory
            </div> 
        </div>
        <div className={"card-addheading list-holder card-header " + 
            (topHidden && characterServices.hasCurrCharacter()? "" : "hidden-list")}> 
            <div className={" list-holder " + !topHidden ? "" : "hidden-list"}>
                <CurrentCharacterForm 
                    formDisplay={currentFormDisplay}
                    toggleForm={currentToggleForm}
                    updateCharacter={updateCurrentCharacter}
                    editData={currentCharacter}/>
                <CurrentCharacter
                    deleteCharacter={deleteCharacter}
                    dropCharacter={dropCharacter}
                    editCharacter={editCurrentCharacter}
                    characterData={currentCharacter}
                    currentActions={currentActions}
                    currentActors={currentActors}
                    currentItems={currentItems}
                    currentStatus={currentStatus}/>
                </div>
            </div>
            <div className={"card-addheading  bg-primary text-white card-header"} onClick={toggleBottomList} > 
                <div className={bottomHidden ? "" : "hidden-list"}>
                    <FaChevronDown/> All Items
                </div>
                <div className={!bottomHidden ? "" : "hidden-list"}>
                    <FaChevronUp /> All Items
                </div> 
            </div>
            <div className={"card-addheading list-holder card-header " + 
                (bottomHidden? "" : "hidden-list")}>
            <div className={" list-holder " + !bottomHidden ? "" : "hidden-list"}>
                <CharacterForm 
                    formDisplay={userFormDisplay}
                    toggleForm={userToggleForm}
                    createCharacter={createCharacter}
                    updateCharacter={updateCharacter}
                    selectCharacter={selectCharacter}
                    cancelForm={cancelEdit}
                    editData={editData}/>
                <CharacterSearch
                    orderBy={userOrderBy}
                    orderDir={userOrderDir}
                    changeOrder={userChangeOrder}
                    searchCharacters={userSearch}/>
                <CharacterList 
                    characters={userFilteredCharacters} 
                    selectCharacter={selectCharacter}
                    deleteCharacter={deleteCharacter}
                    editCharacter={editCharacter}/>
            </div>
        </div>
        </>
    )
}

export default CharacterPage;