import CurrentCharacter from '../Components/Player/Characters/CurrentCharacter';
import * as auth from './user-services';

export function hasCurrCharacter() {
    const currCharacter = localStorage.getItem('character');
    return currCharacter != null;
}

export function getCharId () {
    return localStorage.getItem('character');
}

export function setCharId (characterId) {
    localStorage.setItem('character', characterId);
}

export function clearCharacter () {
    localStorage.removeItem('character');
}

export async function createCharacter(character) {
    const result = await fetch(`/api/character`, {
        method: 'post',
        body: JSON.stringify(character),
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function getCharacters(userId) {
    const result = await fetch(`/api/character/${userId}`, {
        method: 'get',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function getGameCharacters(gameId) {
    const result = await fetch(`/api/character/game/${gameId}`, {
        method: 'get',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function updateCharacter(character) {
    const result = await fetch(`/api/character/${character._id}`, {
        method: 'put',
        body: JSON.stringify(character),
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function deleteCharacter(characterId) {
    const result = await fetch(`/api/character/${characterId}`, {
        method: 'delete',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}

export async function deleteAllCharacters(userId) {
    const result = await fetch(`/api/character/all/${userId}`, {
        method: 'delete',
        headers: auth.getHeader()
    });
    const body = await result.json();
    return body;
}