import React, {useState, useContext, useEffect} from 'react';
import {AppContext} from '../../../Services/AppContext';
import ItemList from './ItemList';
import ItemForm from './ItemForm';
import ItemSearch from './ItemSearch';
import ItemWrapperForm from './ItemWrapperForm';
import ItemWrapperList from './ItemWrapperList';
import ItemWrapperSearch from './ItemWrapperSearch';
import * as itemServices from './../../../Services/item-services';
import * as characterServices from './../../../Services/character-services';
import * as userServices from './../../../Services/user-services';
import * as gameServices from './../../../Services/game-services';
import { find, findIndex, without } from 'lodash';
import { FaChevronDown, FaChevronUp } from 'react-icons/fa';

const ItemPage = () => {  
    const contextData = useContext(AppContext)[0];
    const [items, setItems] = contextData.user.items;
    const [characterItems, setCharacterItems] = contextData.character.items;
    const [defaultItems, setDefaultItems] = contextData.default.items;

    const [user, setUser] = contextData.user.user;
    const [character, setCharacter] = contextData.character.character;

    const [userFormDisplay, setUserFormDisplay] = useState(false);
    const [characterFormDisplay, setCharacterFormDisplay] = useState(false);
    const [bottomHidden, setBottomHidden] = useState(false);
    const [topHidden, setTopHidden] = useState(false);
    const toggleTopList = () => {
        setTopHidden(!topHidden);
    }
    const toggleBottomList = () => {
        setBottomHidden(!bottomHidden);
    }
    const userToggleForm = () => {
        setUserFormDisplay(!userFormDisplay);
    }
    const characterToggleForm = () => {
        setCharacterFormDisplay(!characterFormDisplay);
    }

    const [userQuery, setUserQuery] = useState('');
    const [characterQuery, setCharacterQuery] = useState('');
    const userSearch = (query) => {
        setUserQuery(query);
    }
    const characterSearch = (query) => {
        setCharacterQuery(query);
    }

    const [userOrderBy, setUserOrderBy] = useState('name');
    const [userOrderDir, setUserOrderDir] = useState('asc');
    const [characterOrderBy, setCharacterOrderBy] = useState('name');
    const [characterOrderDir, setCharacterOrderDir] = useState('asc');
    const userChangeOrder = (order, dir) => {
        setUserOrderBy(order);
        setUserOrderDir(dir);
    }
    const characterChangeOrder = (order, dir) => {
        setCharacterOrderBy(order);
        setCharacterOrderDir(dir);
    }
    
    const [userShowCustom, setUserShowCustom] = useState(true);
    const [userShowDefault, setUserShowDefault] = useState(true);
    const [userShowConsume, setUserShowConsume] = useState(true);
    const [userShowNonConsume, setUserShowNonConsume] = useState(true);
    const [characterShowCustom, setCharacterShowCustom] = useState(true);
    const [characterShowDefault, setCharacterShowDefault] = useState(true);
    const [characterShowConsume, setCharacterShowConsume] = useState(true);
    const [characterShowNonConsume, setCharacterShowNonConsume] = useState(true);
    const [characterShowEquiped, setCharacterShowEquiped] = useState(true);
    const [characterShowNotEquiped, setCharacterShowNotEquiped] = useState(true);
    const userChangeShow = (showCustom, showDefault, showConsume, showNonConsume) => {
        setUserShowCustom(showCustom);
        setUserShowDefault(showDefault);
        setUserShowConsume(showConsume);
        setUserShowNonConsume(showNonConsume);
    }
    const characterChangeShow = (showCustom, showDefault, showConsume, showNonConsume, showEquiped, showNotEquiped) => {
        setCharacterShowCustom(showCustom);
        setCharacterShowDefault(showDefault);
        setCharacterShowConsume(showConsume);
        setCharacterShowNonConsume(showNonConsume);
        setCharacterShowEquiped(showEquiped);
        setCharacterShowNotEquiped(showNotEquiped);
    }

    const [editData, setEditData] = useState();
    const [wrapperData, setWrapperData] = useState();
    const [sendData, setSendData] = useState();
    const updateItem = async (updatedItem) => {
        let i = 0;
        for(const item of characterItems) {
            if (item._id == updatedItem) {
                characterItems[i] = updatedItem;
            }
            i++;
        }

        const userIndex = findIndex(items, {
            _id: updatedItem._id
        });
        if (userIndex > -1) {
            items[userIndex] = updatedItem;
            setItems([...items]);
        }
        setEditData();
        await itemServices.updateItem(updatedItem);
    }
    const updateCharacterItem = async (updatedItem, updatedWrapper, originalWrapper) => {
        const userIndex = findIndex(items, {
            _id: updatedItem._id
        });
        if (userIndex > -1) {
            items[userIndex] = updatedItem;
            setItems([...items]);
        }

        let i = 0;
        for(const item of characterItems) {
            if (item._id == updatedItem._id) {
                characterItems[i] = updatedItem;
            }
            i++;
        }
        setCharacterItems([...characterItems]);

        updateWrapper(updatedWrapper, originalWrapper);
        await itemServices.updateItem(updatedItem);
    }
    const updateWrapper = async (updatedWrapper, originalWrapper) => {
        const itemIndex = findIndex(character.items, {
            active: originalWrapper.active,
            usesLeft: originalWrapper.usesLeft,
            recoveryDate: originalWrapper.recoveryDate,
            itemId: updatedWrapper.itemId
        });
        if (itemIndex > -1) {
            const newCharacter = character;
            newCharacter.items[itemIndex] = updatedWrapper;
            setCharacter(newCharacter);
            await characterServices.updateCharacter(newCharacter);
        }
        setEditData();
        setWrapperData()
    }
    const cancelEdit = () => {
        setEditData();
    }
    const cancelCharacterEdit = () => {
        setEditData();
        setWrapperData()
    }
    const editItem = (item) => {
        setEditData(item);
        setUserFormDisplay(true);
        setCharacterFormDisplay(false);
    }
    const editCharacterItem = (item, wrapper) => {
        setEditData(item);
        setWrapperData(wrapper)
        setUserFormDisplay(false);
        setCharacterFormDisplay(true);
    }
    const createItem = async (createdItem) => {
        const newItem = (await itemServices.createItem(createdItem)).item;
        setItems([...items, newItem]);
        const newUser = user;
        newUser.itemIds.push(newItem._id);
        setUser(newUser);
        await userServices.updateUser(newUser)
        return newItem._id;
    }
    const createCharacterItem = async (newCharacterItem, newItemWrapper) => {
        const newItem = (await itemServices.createItem(newCharacterItem)).item;
        setItems([...items, newItem]);
        setCharacterItems([...characterItems, newItem])

        const newUser = user;
        newUser.itemIds.push(newItem._id);
        setUser(newUser);
        await userServices.updateUser(newUser)

        const newCharacter = character;
        newCharacter.items.push({
            active: newItemWrapper.active,
            recoveryDate: newItemWrapper.recoveryDate,
            usesLeft: newItemWrapper.usesLeft,
            itemId: newItem._id});
        setCharacter(newCharacter);
        await characterServices.updateCharacter(newCharacter);

        return newItem._id;
    }
    const selectItem = async (newCharacterItem) => {
        setCharacterItems([...characterItems, newCharacterItem]);

        const newCharacter = character;
        newCharacter.items.push({
            active: false,
            recoveryDate: 'Never',
            usesLeft: newCharacterItem.uses,
            itemId: newCharacterItem._id});
        setCharacter(newCharacter);
        await characterServices.updateCharacter(newCharacter)
    }
    const deleteItem = async (deletedItem) => {
        if (!deletedItem.isDefault) {
                let itemWrapperIndex = -1;
                do {
                    const itemIdIndex = user.itemIds.indexOf(deletedItem._id);
                    if (itemIdIndex > -1) {
                        const newIdList = without(user.itemIds, user.itemIds[itemIdIndex]);
                        const newUser = user;
                        newUser.itemIds = newIdList;
                        setUser(newUser);
                    }
                    
                    const itemsIndex = findIndex(items, {
                        _id: deletedItem._id
                    });
                    if (itemsIndex > -1) {
                        const newList = without(items, items[itemsIndex]);
                        setItems([...newList]);
                    }

                    const charItemsIndex = findIndex(characterItems, {
                        _id: deletedItem._id
                    });
                    if (charItemsIndex > -1) {
                        const newCharList = without(characterItems, characterItems[charItemsIndex]);
                        setCharacterItems([...newCharList]);
                    }
        
                    if (character) {
                        itemWrapperIndex = findIndex(character.items, {
                            itemId: deletedItem._id
                        });
                        if (itemWrapperIndex > -1) {
                            const newCharacter = character;
                            newCharacter.items = without(character.items, character.items[itemWrapperIndex]);;
                            setCharacter(newCharacter);
                        };
                    }
                } while (itemWrapperIndex > -1)
                await userServices.updateUser(user);
                await characterServices.updateCharacter(character)
        } else {
            console.log("can't delete default");
        }
    }
    const dropItem = async (dropItem, itemWrapper) => {
        const charItemsIndex = findIndex(characterItems, {
            itemId: dropItem._id
        });
        const newCharList = without(characterItems, characterItems[charItemsIndex]);
        setCharacterItems([...newCharList]);
        const itemWrapperIndex = findIndex(character.items, {
            itemId: itemWrapper.itemId,
            active: itemWrapper.active,
            usesLeft: itemWrapper.usesLeft,
            recoveryDate: itemWrapper.recoveryDate
        });
        const newCharacter = character;
        newCharacter.items = without(character.items, character.items[itemWrapperIndex]);
        setCharacter(newCharacter);
        await characterServices.updateCharacter(newCharacter);
    }

    let userFilteredItems = [];
    let userOrder;
    userFilteredItems = [...items, ...defaultItems];
    if (userOrderDir === 'asc') {
        userOrder = 1;
    } else {
        userOrder = -1;
    }
    userFilteredItems = userFilteredItems.sort((a, b) => {
        if (
            a[userOrderBy].toLowerCase() <
            b[userOrderBy].toLowerCase()
        ) {
            return -1 * userOrder;
        } else {
            return 1 * userOrder;
        }
        }).filter(eachItem => {
            return ((userShowCustom && !eachItem.isDefault) || 
                (userShowDefault && eachItem.isDefault)) && 
                ((userShowConsume && eachItem.isConsumed) || 
                (userShowNonConsume && !eachItem.isConsumed))
        }).filter(eachItem => {
        return (
            eachItem['name']
            .toLowerCase()
            .includes(userQuery.toLowerCase()) ||
            eachItem['goldValue']
            .toLowerCase()
            .includes(userQuery.toLowerCase()) ||
            eachItem['weight']
            .toLowerCase()
            .includes(userQuery.toLowerCase()) ||
            eachItem['actionType']
            .toLowerCase()
            .includes(userQuery.toLowerCase())
        );
    });

    let characterFilteredItems = [];
    if(character) {
        let characterOrder;
        characterFilteredItems = character.items.map(item => {
            const combinedItem = {
                item: find(characterItems, ['_id', item.itemId]),
                wrapper: item
            }
            return combinedItem
        });
        if (characterOrderDir === 'asc') {
            characterOrder = 1;
        } else {
            characterOrder = -1;
        }
        characterFilteredItems = characterFilteredItems.sort((a, b) => {
            if (
                a.item[characterOrderBy].toLowerCase() <
                b.item[characterOrderBy].toLowerCase()
            ) {
                return -1 * characterOrder;
            } else {
                return 1 * characterOrder;
            }
            }).filter(eachItem => {
                return ((characterShowCustom && !eachItem.item.isDefault) || 
                    (characterShowDefault && eachItem.item.isDefault)) &&
                    ((characterShowConsume && eachItem.item.isConsumed) ||
                    (characterShowNonConsume && !eachItem.item.isConsumed)) &&
                    ((characterShowEquiped && eachItem.wrapper.active) ||
                    (characterShowNotEquiped && !eachItem.wrapper.active))
            }).filter(eachItem => {
            return (
                eachItem.item['name']
                .toLowerCase()
                .includes(characterQuery.toLowerCase()) ||
                eachItem.item['goldValue']
                .toLowerCase()
                .includes(characterQuery.toLowerCase()) ||
                eachItem.item['weight']
                .toLowerCase()
                .includes(characterQuery.toLowerCase()) ||
                eachItem.item['actionType']
                .toLowerCase()
                .includes(characterQuery.toLowerCase())
            );
        })
    }

    return (
        <>
        <div className={"card-addheading bg-primary text-white card-header " + 
            (characterServices.hasCurrCharacter()? "" : "hidden-list")} onClick={toggleTopList} > 
            <div className={topHidden ? "" : "hidden-list"}>
                <FaChevronDown/> Character's Inventory
            </div>
            <div className={!topHidden ? "" : "hidden-list"}>
                <FaChevronUp /> Character's Inventory
            </div> 
        </div>
        <div className={"card-addheading list-holder card-header " + 
            (topHidden? "" : "hidden-list")}> 
            <div className={" list-holder " + !topHidden ? "" : "hidden-list"}>
                <ItemWrapperForm 
                    formDisplay={characterFormDisplay}
                    toggleForm={characterToggleForm}
                    createItem={createCharacterItem}
                    updateItem={updateCharacterItem}
                    updateWrapper={updateWrapper}
                    cancelForm={cancelCharacterEdit}
                    editData={editData}
                    wrapperData={wrapperData}/>
                <ItemWrapperSearch 
                    orderBy={characterOrderBy}
                    orderDir={characterOrderDir}
                    changeOrder={characterChangeOrder}
                    searchItems={characterSearch}
                    showCustom={characterShowCustom}
                    showDefault={characterShowDefault}
                    showConsume={characterShowConsume}
                    showNonConsume={characterShowNonConsume}
                    showEquiped={characterShowEquiped}
                    showNotEquiped={characterShowNotEquiped}
                    changeShow={characterChangeShow}/>
                <ItemWrapperList 
                    items={characterFilteredItems}
                    dropItem={dropItem}
                    deleteItem={deleteItem}
                    editItem={editCharacterItem}/>
            </div>
        </div>
        <div className={"card-addheading  bg-primary text-white card-header"} onClick={toggleBottomList} > 
            <div className={bottomHidden ? "" : "hidden-list"}>
                <FaChevronDown/> All Items
            </div>
            <div className={!bottomHidden ? "" : "hidden-list"}>
                <FaChevronUp /> All Items
            </div> 
        </div>
        <div className={"card-addheading list-holder card-header " + 
            (bottomHidden? "" : "hidden-list")}>
            <div className={" list-holder " + !bottomHidden ? "" : "hidden-list"}>
                <ItemForm 
                    formDisplay={userFormDisplay}
                    toggleForm={userToggleForm}
                    createItem={createItem}
                    updateItem={updateItem}
                    cancelForm={cancelEdit}
                    editData={editData}/>
                <ItemSearch 
                    orderBy={userOrderBy}
                    orderDir={userOrderDir}
                    changeOrder={userChangeOrder}
                    searchItems={userSearch}
                    showCustom={userShowCustom}
                    showDefault={userShowDefault}
                    showConsume={userShowConsume}
                    showNonConsume={userShowNonConsume}
                    changeShow={userChangeShow}/>
                <ItemList 
                    items={userFilteredItems} 
                    selectItem={selectItem}
                    deleteItem={deleteItem}
                    editItem={editItem}/>
            </div>
        </div>
        </>
    )
}

export default ItemPage;