import React, {useState, useEffect} from 'react';
import {FaPlusCircle, FaCheck, FaWindowClose} from 'react-icons/fa';
import * as userServices from './../../../Services/user-services';

const ActorWrapperForm = ({formDisplay, toggleForm, createActor, updateActor,
    editData, updateWrapper, wrapperData, cancelForm}) => {
    const [_id, setId] = useState("");
    const [title, setTitle] = useState("");
    const [isDefault, setIsDefault] = useState(false);
    const [description, setDescription] = useState("");
    const [actorType, setActorType] = useState("NPC");
    const [initMod, setInitMod] = useState("");
    const [initRaw, setInitRaw] = useState("");

    const [isHidden, setIsHidden] = useState(false);
    const [initActual, setInitActual] = useState("");
    const [name, setName] = useState("");

    useEffect(() => {
        if(editData) {
            setId(editData._id);
            setTitle(editData.title);
            setIsDefault(editData.isDefault)
            setDescription(editData.description);
            setActorType(editData.actorType);
            setInitMod(editData.initMod);
            setInitRaw(editData.initRaw);
        }
        if(wrapperData) {
            setIsHidden(wrapperData.isHidden);
            setInitActual(wrapperData.initActual);
            setName(wrapperData.name);
        }
    }, [editData, wrapperData])


    const resetForm = () => {
        toggleForm();
        setId("");
        setTitle("");
        setIsDefault(false);
        setDescription("");
        setActorType("NPC");
        setInitMod("");
        setInitRaw("");

        setIsHidden(false);
        setInitActual("");
        setName("");
    }
    const makeNewActor = () => {
        return {
            title: title,
            isDefault: false,
            description: description,
            actorType: actorType,
            initMod: initMod,
            initRaw: initRaw
        }
    }
    const makeNewWrapper = () => {
        return {
            isHidden: isHidden,
            initActual: initActual,
            name: name,
            actorId: ""
        }
    }
    const createPressed = async (e) => {
        e.preventDefault();
        const newActor = makeNewActor();
        const newWrapper = makeNewWrapper();
        if (createActor) {
            newWrapper.actorId = await createActor(newActor, newWrapper);
        }
        resetForm();
    }
    const updatePressed = async (e) => {
        e.preventDefault();
        const newActor = makeNewActor();
        const newWrapper = makeNewWrapper();
        if (isDefault) {
            if (createActor) {
                newActor._id = await createActor(newActor, newWrapper);
            }
        } else {
            if (updateActor) {
                newActor._id = _id;
                newWrapper.actorId = _id;
                updateActor(newActor, newWrapper, wrapperData);
            }
        }
        resetForm();
    } 
    const updateWrapperPressed = async (e) => {
        e.preventDefault();
        const newWrapper = makeNewWrapper();
        newWrapper.actorId= _id;
        if (newWrapper.actorId && updateWrapper) {
            updateWrapper(newWrapper, wrapperData);
        }
        resetForm();
    }

    const cancelPressed = (e) => {
        e.preventDefault();
        if (cancelForm) {
            cancelForm();
        }
        resetForm();
    }
    const updateActorType = (e, newValue) => {
        e.preventDefault();
        setActorType(newValue);
    }


    return (
        <>
        <div className={'card textcenter mt-3 ' + (formDisplay ? '' : 'add-card')}>
        <div className="card-addheading card-header bg-primary text-white" onClick={cancelPressed}>
            <FaPlusCircle /> Create Creature
        </div>
        <div className="card-body">            
            <form className="cardForm" noValidate>

            <div className="form-group form-row">
            <div className="form-check col-md-3 text-md-right" onClick={() => setIsHidden(!isHidden)}>
                <label className="form-check-label col-md-10" htmlFor="flexCheckChecked">
                Is This Hidden From Others: 
                </label>
                <input className="form-check-input col-md-3" type="checkbox"
                    checked={isHidden} onChange={(e) => setIsHidden(e.target.checked)}/>
            </div>
            </div>


            <div className="form-group form-row">
                <label className="col-md-2 col-form-label text-md-right" htmlFor="name" readOnly>
                    Name: 
                </label>
                <div className="col-md-10">
                    <input type="text" className="form-control" name="name" placeholder="Character's Name"
                    value={name} onChange={(e) => setName(e.target.value)}/>
                </div>
            </div>
            <div className="form-group form-row">
                <label className="col-md-2 col-form-label text-md-right" htmlFor="initActual" readOnly>
                    Initiative Actual
                </label>
                <div className="col-md-10">
                    <input type="text" className="form-control" name="initActual" placeholder="Current Initialtive"
                    value={initActual} onChange={(e) => setInitActual(e.target.value)}/>
                </div>
            </div>

            <div className="form-group form-row ">
                <div className={"offset-md-2 col-md-10 "+(!_id ?  "hidden-button" : "")}>
                <button type="submit" className="btn btn-primary d-block ml-auto"
                    onClick={updateWrapperPressed}>
                    <FaCheck /> Update Creature
                </button>
                </div>
            </div>

            <div className="form-group form-row">
                <label className="col-md-2 col-form-label text-md-right" htmlFor="title" readOnly>
                    Creature's Title:
                </label>
                <div className="col-md-10">
                    <input type="text" className="form-control" name="title" placeholder="Creature Title"
                    value={title} onChange={(e) => setTitle(e.target.value)}/>
                </div>
            </div>

            <div className="form-group form-row">
                <label className="col-md-2 col-form-label text-md-right" htmlFor="initMod" readOnly>
                    Initiative Modifier:
                </label>
                <div className="col-md-10">
                    <input type="text" className="form-control" name="initMod" placeholder="Initiative Modifier"
                    value={initMod} onChange={(e) => setInitMod(e.target.value)}/>
                </div>
            </div>

            <div className="form-group form-row">
                <label className="col-md-2 col-form-label text-md-right" htmlFor="initRaw" readOnly>
                    Initiative Default (optional):
                </label>
                <div className="col-md-10">
                    <input type="text" className="form-control" name="initRaw" placeholder="Default Initiative"
                    value={initRaw} onChange={(e) => setInitRaw(e.target.value)}/>
                </div>
            </div>

            <label className="col-md-2 col-form-label text-md-right" htmlFor="actorType" readOnly>
                Actor Type:
            </label>
            <button type="button" className="btn btn-primary dropdown-toggle" data-toggle="dropdown"
                aria-haspopup="true"aria-expanded="false">
                {actorType} <span className="caret" />
            </button>
            <div className="sort-menu dropdown-menu dropdown-menu-right">
                <button className={'sort-by dropdown-item ' + (actorType === 'PC' ? 'active' : '')}
                    onClick={(e) => updateActorType(e, "PC")} href="#">
                    Player Character
                </button>
                <button className={'sort-by dropdown-item ' + (actorType === 'Pet' ? 'active' : '')}
                    onClick={(e) => updateActorType(e, "Pet")} href="#">
                    Pet
                </button>
                <button className={'sort-by dropdown-item ' + (actorType === 'Familiar' ? 'active' : '')}
                    onClick={(e) => updateActorType(e, "Familiar")} href="#">
                    Familiar
                </button>
                <button className={'sort-by dropdown-item ' + (actorType === 'Companion' ? 'active' : '')}
                    onClick={(e) => updateActorType(e, "Companion")} href="#">
                    Animal Companion
                </button>
                <button className={'sort-by dropdown-item ' + (actorType === 'Ally' ? 'active' : '')}
                    onClick={(e) => updateActorType(e, "Ally")} href="#">
                    Ally
                </button>
                <button className={'sort-by dropdown-item ' + (actorType === 'NPC' ? 'active' : '')}
                    onClick={(e) => updateActorType(e, "NPC")} href="#">
                    NPC
                </button>
                <button className={'sort-by dropdown-item ' + (actorType === 'Enemy' ? 'active' : '')}
                    onClick={(e) => updateActorType(e, "Enemy")} href="#" >
                    Enemy
                </button>
                <button className={'sort-by dropdown-item ' + (actorType === 'Lair' ? 'active' : '')}
                    onClick={(e) => updateActorType(e, "Lair")} href="#" >
                    Lair Action
                </button>
                <button className={'sort-by dropdown-item ' + (actorType === 'LegendaryEnemy' ? 'active' : '')}
                    onClick={(e) => updateActorType(e, "Legendary")} href="#" >
                    Legendary Enemy
                </button>
                <button className={'sort-by dropdown-item ' + (actorType === 'LegendaryAction' ? 'active' : '')}
                    onClick={(e) => updateActorType(e, "LegendaryAction")} href="#" >
                    Legendary Action
                </button>
                <button className={'sort-by dropdown-item ' + (actorType === 'Environment' ? 'active' : '')}
                    onClick={(e) => updateActorType(e, "Environment")} href="#" >
                    Environment
                </button>
            </div>
           
            <div className="form-group form-row">
                <label className="col-md-2 text-md-right" htmlFor="description">
                    Description
                </label>
                <div className="col-md-10">
                    <textarea className="form-control" rows="4" cols="50" name="description" id="description"
                    placeholder="Actor Description..." value={description} onChange={(e) => setDescription(e.target.value)}/>
                </div>
            </div>

            <div className="form-group form-row mb-0 ">
                <div className={"offset-md-2 col-md-10 "+(!_id? "" : "hidden-button" )}>
                    <button type="submit" 
                    className="btn btn-primary d-block ml-auto" 
                        onClick={createPressed}>
                        <FaPlusCircle /> Create Creature
                    </button>
                </div>
                <div className={"offset-md-2 col-md-10 "+(!_id ?  "hidden-button" : "")}>
                    <button type="submit" className="btn btn-primary d-block ml-auto"
                        onClick={updatePressed}>
                        <FaCheck /> Update Creature
                    </button>
                </div>
                <div className="offset-md-2 col-md-10">
                    <button type="submit" className="btn btn-primary d-block ml-auto" onClick={cancelPressed}>
                        <FaWindowClose /> Cancel
                    </button>
                </div>
            </div>
            </form>
        </div>
        </div>
        </>
    )
}

export default ActorWrapperForm;










